.alphabet-section {
	padding-bottom: 50px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.4);
	margin-bottom: 48px;
	@include media-breakpoint-down(sm) {
		padding-bottom: 30px;
		margin-bottom: 20px; }
	&:last-of-type {
		margin-bottom: 0;
		border-bottom: none; }
	&__side-title {
		color: $gray-800;
		font-size: 16px;
		font-weight: 400;
		margin-bottom: 14px;
		display: block;
		@include media-breakpoint-down(xxl) {
			font-size: 15px; }
		@include media-breakpoint-down(md) {
			font-size: 13px;
			margin-bottom: 28px; }
		@include media-breakpoint-down(sm) {
			font-size: 13px;
			margin-bottom: 20px; } }
	&__nested-list {
		padding-left: 10px;
		margin-bottom: 16px;
		transition: 0.3s; }
	&__content-item {
		display: flex;
		position: relative;
		padding-bottom: 36px;
		border-bottom: 1px solid #d9d9d9;
		margin-bottom: 25px;
		&:last-child {
			margin-bottom: 0;
			border-bottom: 0; }
		@include media-breakpoint-down(sm) {
			flex-direction: column;
			font-size: 13px;
			padding-bottom: 25px; } }
	&__nested-item {
		font-size: 14px;
		margin-bottom: 5px;
		list-style: none;
		font-weight: 400;
		width: 100% !important;
		@include media-breakpoint-down(sm) {
			width: 100%;
			font-size: 13px !important; } }
	&__char-title {
		position: absolute;
		top: 0;
		left: -100px;
		font-size: 28px;
		font-weight: 500;
		@include media-breakpoint-down(sm) {
			position: static;
			font-size: 22px;
			margin-bottom: 15px; } }
	&__dropdown-item {
		font-weight: 400;
		padding: 0;
		margin-bottom: 10px;
		text-decoration: none;
		background: none;
		display: inline-block; }
	&__dropdown {
		position: static;
		&.dropdown-menu {
			display: flex;
			background: none;
			width: 100%;
			border: none;
			box-shadow: none;
			padding: 0;
			margin: 0;
			z-index: 8; }
		.dropdown-item {
			padding: 0;
			margin-bottom: 10px;
			text-decoration: none;
			background: none;
			&:hover {
				color: $gray-800; }
			&:after {
				transform: rotate(180deg);
				transition: 0.3s; }
			&.collapsed:after {
				transform: none; } }
		.dropdown-item.active, .dropdown-item:active {
			color: $gray-800;
			background: none; }
		& > ul {
			padding: 0;
			margin: 0;
			list-style: none;
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			li {
				width: 33%;
				@include media-breakpoint-down(md) {
					width: 50%;
					font-size: 15px; }
				@include media-breakpoint-down(sm) {
					width: 100%;
					font-size: 14px; } } } } }


