.b-colors {
  $_: &;
  display: flex;
  align-items: center;
  &__list {
    display: flex; }
  &__item {
    @include wh(12px);
    border-radius: 50%;
    margin-left: 2px;
    &:first-child {
      margin-left: 0; }
    &:nth-child(n+6) {
      display: none; }
    @include media-breakpoint-down(md) {
      @include wh(10px); }
    #{$_}_itemsTwo & {
      @include wh(14px);
      @include media-breakpoint-down(md) {
        @include wh(14px); } } }
  &__more {
    font-size: 11px;
    margin-left: 10px;
    .item & {
      opacity: 0.7; } } }
