.alphabet-menu {
  margin-bottom: 54px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  padding: 18px 0;
  @include media-breakpoint-down(xxl) {
    margin-bottom: 35px; }
  @include media-breakpoint-down(md) {
    margin-bottom: 25px; }
  @include media-breakpoint-down(sm) {
    margin-bottom: 20px; }
  &__side-title {
    color: $gray-800;
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 14px;
    display: block;
    @include media-breakpoint-down(xxl) {
      font-size: 15px; }
    @include media-breakpoint-down(md) {
      font-size: 13px; } }
  &__content-line {
    display: flex;
    margin-bottom: 4px;
    &:last-child {
      margin-bottom: -6px; } }
  &__content-line-name {
    color: $gray-800;
    margin-right: 16px;
    font-size: 16px;
    @include media-breakpoint-down(xxl) {
      font-size: 15px; }
    @include media-breakpoint-down(md) {
      font-size: 14px; } }
  &__list {
    display: flex;
    margin-bottom: 0;
    padding: 0;
    flex-wrap: wrap; }
  &__item {
    list-style: none; }
  &__link {
    font-weight: 500;
    font-size: 16px;
    text-transform: uppercase;
    color: $dark;
    margin-right: 6px;
    margin-bottom: 6px;
    display: block; } }
