.svg-stars {
  $_: &;
  --width: 12px;
  $height: var(--width);
  $repeater: 5;

  width: calc(#{var(--width)}*#{$repeater});
  height: $height;
  background-image: url(~images/star.svg?fill=%23d9d9d9);
  background-size: #{var(--width)} $height;
  background-repeat: repeat-x;
  background-position: 0 0;
  &:before {
    display: block;
    content: '';
    width: 0;
    height: $height;
    background-image: url(~images/star.svg?fill=%23ecc400);
    background-size: #{var(--width)} $height;
    background-repeat: repeat-x;
    background-position: 0 0; }
  @for $i from 1 through $repeater {
    &_#{$i}::before {
      width: calc(#{var(--width)}*#{$i}); } } }
