@import "../../sass/utilities/variables";
@import "../../sass/utilities/functions";
@import "../../sass/utilities/mixins";
//@import ../bootstrap/bootstrap

@keyframes anim {
  0% {
    transform: translateX(100%); }
  100% {
    transform: translateX(0%); } }
@keyframes anim2 {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(0%); } }

.sliderMain {
  --offset-bottom: 85px;
  @include media-breakpoint-up(md) {
    --offset-bottom: 40px; }
  @include media-breakpoint-up(xl) {
    --offset-bottom: 80px; }
  @include media-breakpoint-up(xxl) {
    --offset-bottom: 160px; }
  position: relative;
  height: 100%;
  &::after {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 160px;
    background-image: linear-gradient(to top, rgba(13, 6, 0, 0) 0%, rgba(13, 6, 0, 0.2) 100%);
    @include media-breakpoint-up(md) {
      height: 300px; } }
  &__inner {
    height: 100%; }
  &__content {
    z-index: 1;
    position: absolute;
    left: 0;
    width: 100%;
    bottom: var(--offset-bottom); }
  &__subtitle {
    margin-bottom: 26px;
    font-size: 14px;
    @include media-breakpoint-up(md) {
      margin-bottom: 40px;
      font-size: 16px; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 60px; } }
  &__title {
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 42px;
    letter-spacing: -0.03em;
    @include media-breakpoint-up(md) {
      margin-bottom: 58px;
      font-size: vw(90px); }
    @include up(1921px) {
      font-size: 56px; } }
  &__img {
    width: 100%;
    overflow: hidden;
    opacity: 0.95;
    @include media-breakpoint-down(md) {
      opacity: 0.7; }
    img, source {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__navi {
    position: absolute;
    display: flex;
    align-items: center;
    bottom: var(--offset-bottom);
    @include media-breakpoint-up(md) {
      justify-content: flex-end; }
    @include media-breakpoint-down(md) {
      bottom: 40px; } }
  &__button {
    display: flex;
    align-items: center;
    height: var(--swiper-navigation-size);
    @include media-breakpoint-down(md) {
      font-size: 16px; } }
  .swiper-wrapper, .slide-inner {
    transition-timing-function: cubic-bezier(0.47, 0, 0, 1); }
  .swiper-slide {
    overflow: hidden;
    background-color: #000;
    &[data-theme="white"] {
      color: #fff;
      a {
        color: inherit; } } }
  .slide-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0; }
  .swiper-navigation {
    @include swiper-navigation;
    --swiper-navigation-hover-color: #{$red};
    display: flex;
    @include media-breakpoint-down(md) {
      display: none; } }
  .swiper-pagination {
    --swiper-theme-color: #fff;
    position: relative;
    @include media-breakpoint-down(md);
    @include media-breakpoint-up(md) {
      margin-right: 74px;
      width: 180px;
      height: 1px;
      background-color: rgba(255,255,255,0.5); }
    @include media-breakpoint-up(xl) {
      width: 296px; }
    .swiper-pagination-progressbar-fill {
      transition-duration: calc(var(--delay) + 3000ms) !important;
      transition-timing-function: linear; }
    .tr-none {
      transition: none !important;
      transform: translate3d(0px, 0px, 0px) scaleX(0) scaleY(1) !important; }
    .swiper-pagination-bullet {
      &.swiper-pagination-bullet-active {
        background-color: #fff; } } } }
