.b-delivery2 {
  $_: &;
  margin-bottom: 90px;
  font-weight: 400;
  &_bordered {}

  &__item {
    position: relative;
    z-index: 0;
    margin-bottom: 16px;
    padding-bottom: 10px;
    border-bottom: 1px dashed #d9d9d9;
    @include media-breakpoint-up(lg) {
      display: flex; }
    @include media-breakpoint-down(lg) {
      display: flex;
      flex-wrap: wrap; }
    #{$_}_bordered & {
      border: 1px solid #bfbfbf;
      margin-bottom: 0;
      padding: 20px 0;
      margin-top: -1px;
      &:first-child {
        margin-top: 0; }
      @include media-breakpoint-up(lg) {
        padding: 20px 30px; }
      @include media-breakpoint-down(lg) {
        border-left: 0;
        border-right: 0;
        padding-left: 26px;
        @each $breakpoint, $container-offset in $container-offsets {
          @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
            margin-left: $container-offset * -1;
            margin-right: $container-offset * -1;
            padding-left: $container-offset + 30px;
            padding-right: $container-offset; } } } } }
  &-title {
    margin-top: 12px; }
  &-select {
    display: inline-flex;
    position: relative;
    &:after {
      content: '';
      width: 9px;
      height: 6px;
      display: inline-block;
      vertical-align: middle;
      background: url("~images/arrow.svg") center no-repeat;
      transform: rotate(90deg) translateX(-50%);
      right: -15px;
      top: 52%;
      position: absolute; }
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      &:after {
        top: unset;
        bottom: 6px;
        transform: rotate(90deg); } } }
  &__img {
    flex: none;
    width: 130px;
    margin-bottom: 11px;
    @include media-breakpoint-up(lg) {
      width: 80px;
      margin-right: 20px; }
    @include media-breakpoint-up(xxl) {
      width: 100px; }
    @include media-breakpoint-down(lg) {
      width: 70px;
      height: 46px;
      object-fit: contain;
      margin-bottom: 50px; }
    #{$_}_bordered & {
      @include media-breakpoint-down(lg) {
        width: 68px; } } }
  &__content {
    flex: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    @include media-breakpoint-up(lg) {
      width: 50%; }
    @include media-breakpoint-down(lg) {
      margin-bottom: 3px;
      width: 100%; } }

  &__img + &__content {
    @include media-breakpoint-down(lg) {
      width: calc(100% - 85px);
      margin-top: -5px;
      margin-left: 15px; } }
  &__title {
    margin-bottom: 11px;
    font-size: 14px;
    font-weight: 500;
    @include media-breakpoint-down(lg) {
      margin-bottom: 6px; } }
  &__onmap {
    cursor: pointer;
    border-bottom: 1px dashed currentColor;
    color: #999;
    line-height: 1;
    margin-top: auto;
    transition: 0.3s;
    &:hover {
      color: $dark; }
    @include media-breakpoint-down(lg) {
      margin-top: 5px;
      position: absolute;
      top: 60px;
      left: 0; } }
  &__dayPrice {
    display: flex;
    @include media-breakpoint-up(lg) {
      width: 430px - 30px;
      margin-left: auto; }
    @include media-breakpoint-down(lg) {
      width: 100%; } }
  &__day {
    text-align: right;
    width: 170px;
    @include media-breakpoint-down(xl) {
      width: unset;
      margin: 0 10px;
      max-width: 120px;
      text-align: left; }
    @include media-breakpoint-down(lg) {
      width: 50%;
      text-align: left;
      margin: unset;
      max-width: unset; } }
  &__price {
    font-weight: 500;
    text-align: right;
    margin-left: auto;
    @include media-breakpoint-down(lg) {
      margin-left: 0;
      width: 50%;
      text-align: left; }
    span {
      display: block;
      color: #999; } }
  &__control {
    flex: none;
    align-self: center;
    margin-right: 14px;
    @include media-breakpoint-down(lg) {
      margin-right: 0;
      margin-left: -30px;
      margin-top: 30px;
      margin-bottom: -30px; }
    label {
      position: absolute;
      left: 0;
      top: 0;
      @include wh(100%);
      cursor: pointer;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        @include wh(100%);
        z-index: -1; }
      &:hover::before {
        background-color: $lightgray; } }
    input:checked+label::before {
      background-color: $lightgray; } }
  &__info {
    font-size: 12px;
    display: flex;
    align-items: center;
    @include media-breakpoint-down(lg) {
      margin-left: -30px;
      margin-top: 16px; }
    @include media-breakpoint-up(lg) {
      position: absolute;
      bottom: 20px;
      right: 30px;
      text-align: right; }
    .icon {
      margin-right: 14px;
      @include wh(24px);
      line-height: 24px;
      text-align: center;
      background-color: #bfbfbf;
      font-size: 17px;
      font-weight: 700;
      border-radius: 50%;
      color: #fff;
      &::before {
        content: 'i'; }
      @include media-breakpoint-up(lg) {
        margin-left: 14px;
        order: +1; } } }
  &__item_shop {
    @include media-breakpoint-down(lg) {
      .b-delivery2__img {
        width: 130px;
        height: 90px;
        margin-bottom: 11px; }
      .b-delivery2__content {
        width: 100%;
        margin-top: 0;
        margin-left: 0; }
      .b-delivery2__onmap {
        position: static;
        margin-top: 8px;
        margin-bottom: 12px; } } }
  &-info {
    color: $gray-800;
    line-height: 1.3;
    font-weight: 400;
    font-size: 14px;
    margin-bottom: 30px;
    svg {
      margin-top: 2px; }
    @include media-breakpoint-down(lg) {
      br {
        display: none; } } } }
