.section {}

.section-header {
  $_: &;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 35px;
  @include media-breakpoint-up(md) {
    padding-bottom: 80px; }
  @include media-breakpoint-up(xxl) {
    padding-bottom: 90px; }
  &_padding-top {
    padding-top: 90px;
    @include media-breakpoint-up(xxl) {
      padding-top: 220px; } }
  &_size-md {
    &#{$_}_padding-top {
      padding-top: 40px;
      @include media-breakpoint-up(md);
      @include media-breakpoint-up(xxl) {
        padding-top: 100px; } } }
  &_brands {
    background: url("~images/kit/1-sm.jpg") 0 0 no-repeat;
    background-size: cover;
    @include media-breakpoint-up(md) {
      height: 407px;
      background-image: url("~images/kit/1.jpg"); }
    @include media-breakpoint-up(xxl) {
      height: 657px; } }

  &__title {
    height: 100%;
    display: flex;
    align-items: center;
    align-self: center;
    margin-bottom: 60px;
    @include media-breakpoint-down(md) {
      align-self: flex-start;
      margin-bottom: 20px; }
    &_2 {
      margin-bottom: 0 !important; }
    #{$_}_brands & {
      color: #fff; }
    #{$_}_main & {
      @include media-breakpoint-up(xl) {
        text-align: center; } }
    #{$_}_size-md & {
      @include h2; } }
  &__text {
    color: #8c8c8c;
    #{$_}_brands & {
      color: #fff; } }
  &__navi {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include navi;
    #{$_}_brands & {
      --swiper-pagination-active-color: #fff;
      .swiper-navigation {
        --swiper-navigation-hover-color: #{$red}; } }
 } }    //#{$_}_similar &

.section-footer {
  padding-top: 40px;
  padding-bottom: 115px;
  display: flex;
  justify-content: space-between;
  @include media-breakpoint-up(lg) {
    padding-top: 130px;
    padding-bottom: 130px;
    justify-content: center; }
  @include media-breakpoint-up(xxl) {
    padding-top: 227px;
    padding-bottom: 160px; }
  @include navi;
  &_size-md {
    padding-top: 40px;
    padding-bottom: 50px;
    @include media-breakpoint-up(md) {
      padding-bottom: 140px; }
    @include media-breakpoint-up(xxl) {
      padding-bottom: 180px; } } }
