.old-browser {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100000;
  background: #fff;
  top: 0;
  left: 0;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
  overflow-x: hidden;
  .old &, .demo-container-visible & {
    display: block;
    @include media-breakpoint-down(md) {
      display: flex; } }
  &__logo {
    display: inline-block;
    vertical-align: bottom;
    margin-bottom: 8px;
    font-size: 22px;
    line-height: .7;
    text-decoration: none;
    color: $body-color !important;
    letter-spacing: -.025em;
    position: absolute;
    top: 40px;
    left: 40px;
    &-img {
      display: inline-block;
      margin-right: 10px;
      position: relative;
      top: -7px;
      margin-top: 7px;
      border: none; }
    span {
      padding-top: 3px;
      display: inline-block;
      vertical-align: middle; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 16px; }
    &:hover {
      opacity: .65; }
    [data-theme-color="white"] & {
      color: #fff !important; }
    svg {
      width: 31px;
      height: 26px;
      margin-right: 7px;
      [data-theme-color="white"] & {
        fill: #fff; } } }
  &__content {
    margin: 0 auto;
    max-width: 600px;
    width: 600px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  &__head {
    display: flex;
    position: relative;
    margin-bottom: 38px;
    .alert-icon {
      position: absolute;
      left: -38px;
      top: 0; } }
  &__head-desc {
    font-size: 16px;
    font-weight: 400;
    color: $gray-800;
    max-width: 600px; }
  &__download-title {
    font-size: 28px;
    font-weight: 400;
    margin-bottom: 34px; }
  &__download-list {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 0;
    justify-content: space-between; }
  &__download-item {
    width: 108px;
    float: left; }
  &__download-link {
    display: block;
    text-align: center;
    justify-content: center;
    &:hover, &:active {
      .old-browser__browser-logo img {
        transform: scale(1.2); } } }
  &__browser-logo {
    display: block;
    margin-bottom: 12px;
    border: none;
    img {
      transition: 0.5s;
      border: none; } }
  &__browser-title {
    font-weight: 400; }
  @include media-breakpoint-down(md) {
    justify-content: flex-start;
    padding: 24px;
    &__logo {
      position: static;
      font-size: 18px;
      display: flex;
      align-items: flex-end;
      margin-bottom: 0;
      svg {
        width: 26px;
        height: 22px; } }
    &__logo-img {
      position: static;
      margin-top: 0;
      top: unset; }
    &__head {
      flex-direction: column;
      margin-top: 28px;
      margin-bottom: 23px;
      .alert-icon {
        position: static;
        margin-bottom: 9px; } }
    &__content {
      max-width: 100%;
      width: 100%;
      position: static;
      transform: none; }
    &__head-desc {
      font-size: 12px; }
    &__download-title {
      font-size: 18px;
      line-height: 1.3;
      margin-bottom: 25px; }
    &__download-list {
      flex-direction: column;
      align-items: flex-start; }
    &__download-item {
      width: 100%; }
    &__download-link {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      text-align: left; }
    &__browser-logo {
      width: 36px;
      height: 36px;
      margin-right: 10px;
      display: inline-block;
      vertical-align: middle; }
    &__browser-title {
      font-size: 14px;
      display: inline-block;
      vertical-align: middle;
      padding-bottom: 12px; } } }
