@keyframes anim {
  0% {
    transform: translateX(100%); }
  100% {
    transform: translateX(0%); } }
@keyframes anim2 {
  0% {
    transform: translateX(-100%); }
  100% {
    transform: translateX(0%); } }

.sliderItem {
  .swiper-wrapper, .slide-inner {
    transition-timing-function: cubic-bezier(0.47, 0, 0, 1); }
  .swiper-slide {
    overflow: hidden; }
  .slide-inner {} }


