.b-delivery {
  font-size: 12px;
  @include media-breakpoint-up(xxl) {
    font-size: 14px; }
  &__top {
    padding-bottom: 10px;
    margin-bottom: 25px;
    border-bottom: 1px solid #d9d9d9;
    @include media-breakpoint-down(xl) {
      margin-bottom: 16px; } }
  &__title {
    font-size: 15px;
    @include media-breakpoint-up(xxl) {
      font-size: 16px; } }
  table {
    width: 100%;
    margin-bottom: 10px;
    b {
      color: $body-color; }
    th {
      font-size: 13px;
      font-weight: 500;
      line-height: 1.25;
      padding-bottom: 3px;
      &:last-child {
        text-align: right;
        padding-left: 20px; } }
    td {
      font-size: 12px;
      font-weight: 400;
      line-height: 1.2;
      vertical-align: top;
      color: #666;
      &:last-child {
        text-align: right;
        padding-left: 20px; } } }
  &__all {
    display: inline-block;
    margin-top: 15px;
    border-bottom: 1px dashed $body-color;
    font-size: 14px;
    @include media-breakpoint-up(xxl) {
      font-size: 16px; } } }


