.itemsTwo {
  $_: &;
  position: relative;
  color: #fff;
  overflow: hidden;
  &__item {
    overflow: hidden; }
  &__mainTitle {
    position: absolute;
    z-index: 2;
    top: 50px;
    width: 100%;
    font-size: 24px;
    letter-spacing: -1px;
    pointer-events: none;
    @include media-breakpoint-up(lg) {
      left: 0;
      font-size: 40px;
      text-align: center; }
    @include media-breakpoint-up(xl) {
      top: 93px; }
    @include media-breakpoint-down(xl) {
      font-size: 32px;
      font-weight: 500; } }
  &__slider {
    background-color: $body-color; }
  &__img {
    display: block;
    height: 100%;
    opacity: 0.95;
    @include scale(#{$_}__item);
    img {
      transition: $transition;
      @include media-breakpoint-down(md) {
        width: 100%;
        height: 100%;
        object-fit: cover; } } }

  &__content {
    position: relative;
    z-index: 1; }
  &__contentInner {
    width: 100%;
    position: absolute;
    bottom: 80px;
    @include media-breakpoint-up(lg) {
      max-width: 420px;
      bottom: 40px; }
    @include media-breakpoint-up(xxl) {
      bottom: 90px; } }
  &__labels {
    margin-bottom: 26px;
    @include media-breakpoint-up(md) {
      margin-bottom: 30px; } }
  &__title {
    display: block;
    margin-bottom: 8px;
    line-height: 18px;
    font-size: 16px;
    color: inherit !important;
    text-decoration: none;
    min-height: 50px;
    transition: $transition;
    @include media-breakpoint-down(lg) {
      max-width: 500px; }
    @include media-breakpoint-up(md) {
      font-size: 20px; }
    @include media-breakpoint-up(xl) {
      margin-bottom: 27px;
      font-size: 24px;
      line-height: normal; }
    &:hover {
      opacity: 0.7; } }
  &__colors {}
  &__bottom {
    display: flex;
    align-items: center;
    @include media-breakpoint-down(md) {
      justify-content: space-between; } }
  &__price {
    display: flex;
    align-items: baseline; }
  &__priceOld {
    text-decoration: line-through;
    margin-left: 20px;
    letter-spacing: -0.35px;
    font-weight: 400;
    white-space: nowrap; }
  &__priceNew {
    white-space: nowrap;
    font-size: 16px;
    @include media-breakpoint-up(md) {
      font-size: 24px; } }
  &__options {
    display: flex;
    margin-left: 42px; }
  &__option {
    @include circle;
    @include circle-white;
    @include wh(42px);
    margin-right: 4px;
    @include media-breakpoint-up(lg) {
      @include wh(50px); }
    svg {
      @include wh(10px); }
    &_favorites {}
    &_compare {} }
  &__buttonWrapper {
    display: flex;
    justify-content: flex-end; }
  &__button {
    position: absolute;
    z-index: 2;
    bottom: 30px;
    @include media-breakpoint-up(xxl) {
      bottom: 90px + 10px; } }
  &__arrows {
    @include media-breakpoint-up(lg) {
      display: flex;
      justify-content: flex-end; } }
  .swiper-slide {
    @include media-breakpoint-down(md) {
      height: 460px; }
    &::before {
      content: '';
      height: 50%;
      position: absolute;
      z-index: 1;
      left: 0;
      bottom: 0;
      width: 100%;
      background-image: linear-gradient(to top, rgba(13, 6, 0, 0.55) 0%, rgba(13, 6, 0, 0) 100%);
      transition: $transition;
      @include media-breakpoint-up(md) {
        background-image: linear-gradient(to top, rgba(13, 6, 0, 0.3) 0%, rgba(13, 6, 0, 0.3) 30%, rgba(13, 6, 0, 0) 100%); } }
    &:hover::before {
      opacity: 0; } }
  .swiper-navigation {
    @include swiper-navigation;
    --swiper-navigation-hover-color: #{$red};
    display: flex;
    position: absolute;
    z-index: 2;
    top: 50%;
    transform: translateY(-50%);
    @include media-breakpoint-down(md) {
      display: none; } }
  .swiper-pagination {
    --swiper-theme-color: #fff;
    bottom: 30px;
    @include media-breakpoint-up(lg) {
      display: none; } }
  .swiper-pagination-bullet {
    background-color: #fff; } }
