.recommend-single {
  &__inner {
    overflow: hidden;
    @include media-breakpoint-up(lg) {
      height: 490px;
      display: flex; }
    @include media-breakpoint-up(xxl) {
      height: 600px; }
    @include media-breakpoint-down(lg) {
      padding: 0; } }
  &__left {
    padding: 40px 0 60px;
    @include media-breakpoint-up(lg) {
      width: 40%;
      padding: 40px 0; }
    @include media-breakpoint-up(xxl) {
      padding: 60px 0; } }
  &__right {
    position: relative;
    flex: 1;
    min-width: 0; }
  &__contentMob {
    position: relative; }
  &__logo {
    display: flex;
    align-items: center;
    margin-bottom: 27px;
    height: 70px;
    padding-left: 90px;
    background: url("~images/logo-star.png") left center no-repeat;
    font-size: 16px;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 45px;
      font-size: 20px; } }
  &__stars {
    display: flex;
    align-items: center;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 10px; }
    span {
      font-size: 11px;
      color: #999;
      font-weight: 400;
      margin-left: 6px; } }
  &__title {
    display: block;
    margin-bottom: 16px;
    letter-spacing: -0.95px;
    line-height: 1.2;
    font-size: 26px;
    @include media-breakpoint-up(lg) {
      padding-right: 20px; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 10px;
      font-size: 38px; } }
  &__opts {
    display: flex;
    overflow: hidden;
    padding: 8px 0;
    border-top: 1px solid #dedede;
    border-bottom: 1px solid #dedede;
    span {
      white-space: nowrap;
      text-transform: uppercase;
      font-size: 12px;
      &::before {
        content: '\00a0\00a0\00a0\00a0'; }
      &:first-child::before {
        content: none; }
      @include media-breakpoint-up(xxl) {
        font-size: 16px; } } }
  &__opts-marquee {
    width: calc(100% + 100px);
    padding-left: 50px;
    animation: marquee 15s linear infinite; }
  &__articul {
    padding: 8px 0 0;
    position: relative;
    top: 4px; }
  &__slider {
    height: 100%; }
  &__img {
    img {
      width: 100%;
      height: 100%;
      object-fit: contain; } }
  &__articul {
    display: flex;
    font-size: 12px;
    font-weight: 400;
    color: #999;
    span {
      margin-left: 4px;
      color: #1f1f1f;
      font-weight: 500; } }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 28px;
    padding-top: 9px;
    border-top: 1px solid #e5e5e5;
    @include media-breakpoint-up(lg) {
      margin-top: 16px;
      margin-bottom: 16px; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 30px;
      padding-bottom: 17px; } }

  &__price {
    display: flex;
    align-items: center; }
  &__priceOld {
    margin-right: 16px;
    text-decoration: line-through;
    font-size: 14px;
    color: #999;
    letter-spacing: -0.35px;
    @include media-breakpoint-down(lg) {
      margin-right: 8px;
      font-size: 12px;
      letter-spacing: -0.3px; } }
  &__priceNew {
    font-size: 17px;
    letter-spacing: -0.42px;
    @include media-breakpoint-down(lg) {
      font-size: 13px;
      letter-spacing: -0.33px; } }
  &__optionsWrap {
    position: absolute;
    top: 37px;
    left: 0;
    z-index: 2;
    @include media-breakpoint-up(xxl) {
      top: 60px;
      left: 5px; }
    @include media-breakpoint-down(lg) {
      padding: 0;
      top: 20px;
      left: auto;
      right: 0;
      width: auto; } }
  &__options {
    display: flex;
    flex-direction: column-reverse; }
  &__option {
    @include center;
    @include wh(38px);
    margin-bottom: 4px;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid #d2d2d2;
    background-color: #f7f7f7;
    @include media-breakpoint-up(lg) {
      margin-top: -6px;
      margin-bottom: 0; }
    @include media-breakpoint-up(xxl) {
      @include wh(50px); }
    svg {
      pointer-events: none; }
    &:last-child {
      margin-top: 0; }
    &:hover {
      background-color: #1f1f1f;
      svg {
        fill: #fff; } }
    svg {
      @include wh(10px); } }
  &__footer {
    display: flex;
    margin: 0 calc(var(--padding) * -1); }
  &__button {
    flex: 1;
    height: 70px;
    letter-spacing: -0.07px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 100px;
    color: $body-color;
    cursor: pointer;
    transition: $transition;
    @include media-breakpoint-down(xxl) {
      height: 50px; }
    //  flex-direction: column
    //  svg
    //    margin-bottom: 6px
    svg {
      @include wh(12px);
      transition: $transition;
      margin-right: 6px;
      fill: currentColor;
      @include media-breakpoint-up(xxl) {
        margin-right: 16px;
        @include wh(14px); } }
    &_fast {
      margin-right: 5px;
      background-color: #fff;
      @include media-breakpoint-down(lg) {
        display: none; }
      &:hover {
        background-color: $body-color;
        color: #fff; } }
    &_cart {
      background-color: $body-color;
      color: #fff;
      flex: none;
      @include media-breakpoint-down(lg) {
        border-radius: 0;
        flex: 1;
        height: 64px; }
      @include media-breakpoint-up(lg) {
        width: 134px;
        margin-left: 5px; }
      @include media-breakpoint-up(xxl) {
        flex: 1; }
      &:hover {
        background-color: #fff;
        color: $body-color; } } }
  .swiper-navigation-wrapper {
    @include media-breakpoint-up(lg) {
      position: absolute;
      top: 50%;
      z-index: 2; } }
  .swiper-pagination {
    position: static;
    margin: 10px 0 16px;
    @include media-breakpoint-up(lg) {
      display: none; } }
  .swiper-navigation {
    @include swiper-navigation;
    --swiper-navigation-size: var(--circle-size);
    display: flex;
    justify-content: space-between;
    @include media-breakpoint-down(lg) {
      display: none; } } }

@keyframes marquee {
  0% {
    transform: translateX(100%); }
  100% {
    transform: translateX(-180%); } }
