.b-video {
  width: 180px;
  cursor: pointer;
  @include media-breakpoint-up(xxl) {
    width: 240px; }
  &__img {
    position: relative;
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#0d0600, 0.3);
      opacity: 0;
      transition: $transition-elems; }
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      width: 20px;
      height: 24px;
      transform: translate(-50%, -50%);
      background: #fff;
      clip-path: polygon(0 0, 0% 100%, 100% 50%);
      opacity: 0; }
    &:hover::before, &:hover::after {
      opacity: 1; }
    img {
      width: 100%;
      display: block;
      margin-bottom: 15px; } }
  &__title {
    padding-left: 23px;
    background: url("~images/video.svg") 0 4px no-repeat;
    font-size: 13px;
    @include media-breakpoint-up(xxl) {
      font-size: 14px; } } }

