.compare-block {
	border-top: 1px solid #949494;
	margin-bottom: 50px;
	&:last-of-type {
		margin-bottom: 120px; }
	&__slide {
		will-change: transform; }
	&__title {
		font-size: 18px;
		font-weight: 500;
		margin-bottom: 22px;
		margin-top: 20px;
		cursor: pointer;
		&:after {
			display: inline-block;
			margin-left: 0.5em;
			vertical-align: 0.221em;
			content: "";
			border-top: 0.26em solid;
			border-right: 0.26em solid transparent;
			border-bottom: 0;
			border-left: 0.26em solid transparent;
			position: relative;
			bottom: -1px;
			transform: rotate(180deg);
			transition: 0.3s; }
		&.collapsed {
			&:after {
				transform: none; } } }
	&__content {
		position: relative;
		visibility: visible;
		opacity: 1;
		transition: 0.3s;
		&.collapsed {
			visibility: hidden;
			opacity: 0; } }
	&__prop-titles {
		position: absolute;
		height: 100%;
		width: auto;
		top: 0;
		left: 20px; }
	&__prop-title {
		color: #939393;
		font-size: 14px;
		height: 57px;
		white-space: nowrap;
		//position: absolute
		left: 20px;
		top: 0; }
	&__item {
		display: block;
		font-size: 14px;
		color: $body-color;
		font-weight: 500;
		padding: 21px 20px 11px;
		margin-bottom: 6px;
		border-bottom: 1px solid #e5e5e5;
		position: relative;
		&:last-of-type {
			border-bottom: none; }
		ul {
			margin-top: 10px; }
		.svg-stars {
			display: inline-block;
			vertical-align: middle;
			margin-right: 7px;
			margin-bottom: 3px;
			& + span {
				font-size: 12px; } } }
	@include media-breakpoint-down(xl) {
		&__item {
			padding: 20px 10px 10px; }
		&__prop-titles {
			left: 10px; } }
	@include media-breakpoint-down(sm) {
		&__item {
			padding: 20px 14px 10px 0; }
		&__prop-titles {
			left: 0; } } }
