.delivery-modal {
	&__details-map-close {
		display: flex;
		width: 72px;
		height: 72px;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
		&:hover {
			border: 1px solid $gray-800;
			background: $gray-800; } }
	&__details-map-content {
		position: absolute;
		top: 72px;
		right: 72px;
		width: 445px;
		padding: 60px 20px 100px;
		height: calc(100vh - 130px);
		z-index: 1;
		background: #fff;
		box-shadow: 0px 30px 80px rgba(26, 12, 0, 0.08); }
	&__details-link {
		position: absolute;
		bottom: 60px;
		left: 40px;
		width: 170px;
		text-align: center;
		svg {
			margin-left: 5px; }
		&:hover {
			svg {
				fill: $gray-800; } } }
	&__details-info {
		padding: 0 20px;
		overflow-y: auto;
		height: 100%;
		@include scrollbar; }
	&__map-close {
		display: flex;
		width: 72px;
		height: 72px;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 11;
		&:hover {
			border: 1px solid $gray-800;
			background: $gray-800; } }
	&__wrapper {
		max-width: 100%; }
	&__pagination {
		display: flex;
		justify-content: space-between;
		margin-bottom: 130px;
		margin-top: 100px;
		.catalog-btn {
			&:hover {
				color: $body-color;
				background-color: $lightgray;
				border-color: $lightgray; } }
		nav {
			flex-grow: 1;
			display: flex;
			justify-content: center; }
		.page-item.active .page-link {
			background: $body-color;
			border-color: $body-color; } }
	&__m-details-expand {
		display: none; }
	&__list {
		visibility: hidden;
		opacity: 0;
		transition: 0.5s;
		&.active {
			opacity: 1;
			visibility: visible; } }
	&__map {
		width: 100%;
		visibility: hidden;
		opacity: 0;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 100;
		transition: 0.3s;
		&.active {
			height: 100vh;
			opacity: 1;
			visibility: visible; } }
	&__map-search {
		height: 60px;
		background: #fff;
		border-bottom: 1px solid #d9d9d9;
		.b-search {
			padding: 16px 40px 16px 70px;
			.form-control {
				border-bottom: none; }
			span:first-child {
				left: 30px;
				opacity: 0.3; } } }
	&__map-pano {
		min-height: 100vh;
		max-width: 100vw;
		width: 100vw; }
	&__m-choose-delivery {
		display: none; }
	.ymaps-2-1-78-balloon__content > * {
		height: auto !important;
		width: 450px !important; }
	.ymaps-2-1-78-balloon__close-button {
		filter: invert(1);
		transform: scale(0.7); }
	&__map-list {
		position: absolute;
		top: 72px;
		right: 72px;
		width: 445px;
		height: calc(100vh - 144px);
		background: #fff;
		min-height: calc(100vh - 144px);
		filter: drop-shadow(0px 30px 80px rgba(26, 12, 0, 0.1)); }
	&__map-items-wrap {
		height: calc(100% - 60px);
		overflow-y: auto;
		@include scrollbar; }
	&__m-view-switch-block {
		display: none; }
	.form-switch.form-switch_double {
		display: flex;
		align-items: center;
		padding-left: 0;
		.form-check-label {
			font-weight: 500;
			color: $gray;
			&:first-of-type {
				order: 1;
				color: $body-color; }
			&:last-of-type {
				order: 3; } }
		.form-check-input {
			order: 2;
			position: static;
			margin: 0 7px;
			background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 1'/%3e%3c/svg%3e") !important;
			-webkit-appearance: none;
			&:checked {
				background-color: #fff;
				border: 2px solid #cccccc;
				& ~.form-check-label {
					&:first-of-type {
						color: $gray; }
					&:last-of-type {
						color: $body-color; } } }
			&:focus {
				border: 2px solid #cccccc; } } }
	@include media-breakpoint-down(xl) {
		&__pagination {
			.page-item {
				max-width: unset;
				width: 60px;
				height: 60px; }
			.catalog-btn {
				padding: 0 60px; } } }
	@include media-breakpoint-down(lg) {
		&__m-view-switch-block {
			display: flex;
			min-height: 54px;
			position: absolute;
			top: 0;
			left: 0;
			padding: 0 24px;
			align-items: center;
			z-index: 2;
			background: #fff;
			width: 100%;
			box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.05); }
		&__details-link {
			bottom: 13px;
			left: 24px; }
		&__map-close {
			width: 54px;
			height: 54px; }
		&__details-map-content {
			width: 100%;
			top: calc(100% - 200px);
			right: 0;
			height: 200px;
			padding: 50px 0 50px;
			transition: 0.5s;
			&.expanded {
				top: 90px;
				height: calc(100% - 90px);
				.shops__m-expand-icon {
					transform: rotate(90deg); }
				.shops__m-expand-text {
					&:after {
						content: ''; } } } }
		&__details-info {
			padding: 0; }
		&__m-details-expand {
			display: flex;
			align-items: center;
			position: absolute;
			top: 16px;
			right: 24px;
			cursor: pointer;
			&:hover {
				span {
					color: $body-color; }
				.shops__m-expand-icon {
					background: #666; } }
			span {
				color: $gray-800;
				font-size: 12px;
				font-weight: 400;
				margin-right: 6px;
				transition: 0.3s; } }
		&__m-expand-icon {
			width: 16px;
			height: 16px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: $body-color;
			border-radius: 50%;
			transition: 0.3s;
			transform: rotate(-90deg); }
		&__m-expand-text {
			&:after {
				content: ''; } }
		&__map-list {
			top: 54px;
			left: 0;
			width: 100%;
			max-width: 100%;
			height: calc(100vh - 54px);
			filter: none;
			transition: 0.3s;
			&.hidden {
				opacity: 0;
				visibility: hidden; } }
		&__map {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 100; }
		&__map-pano {
			max-width: 100%;
			width: 100%;
			min-height: calc(100vh - 54px);
			height: calc(100vh - 54px);
			min-width: 100vw;
			position: absolute;
			top: 54px;
			left: 0; }
		&__m-choose-delivery {
			display: flex;
			position: fixed;
			bottom: 0;
			left: 0;
			width: 100%;
			justify-content: center;
			align-items: center;
			height: 54px;
			border-radius: 0;
			z-index: 10; }
		.ymaps-2-1-78-balloon__content > * {
			width: auto !important; }
		.ymaps-2-1-78-balloon-pane, .ymaps-2-1-78-balloon-overlay, .ymaps-2-1-78-balloon-overlay > * {
			height: calc(100vh - 54px) !important;
			z-index: 6010 !important; }
		.b-search {
			padding: 16px 24px 16px 60px;
			span:first-child {
				left: 24px; } } }
	@include media-breakpoint-down(md) {
		margin: 0 -24px;
		&__pagination {
			padding: 0 24px;
			flex-direction: column;
			align-items: center;
			margin-top: 60px;
			margin-bottom: 100px;
			nav {
				width: 100%; }
			.pagination {
				width: 100%;
				.page-item {
					flex-grow: 1; } }
			.catalog-btn {
				width: 100%; } } } }
