.catalog-info {
  padding-top: 30px;
  border-top: 1px solid #d9d9d9;
  font-weight: 400;
  font-size: 15px;
  @include media-breakpoint-up(xxl) {
    font-size: 16px; }
  p {
    color: #666; }
  .hr {
    border-top: 1px solid #d9d9d9; }
  ul {
    margin-left: -8px; }
  li {
    margin-bottom: 20px;
    color: $body-color;
    font-weight: 500; }
  &__title {
    max-width: 1050px;
    margin-bottom: 30px;
    font-size: 17px;
    @include media-breakpoint-up(xl) {
      margin-bottom: 60px;
      font-size: 32px; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 80px;
      font-size: 40px; } }
  &__img {
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        margin-right: $container-offset * -1; } }
    img {
      width: 100%; } } }
