.b-insta {
  $_: &;
  background-color: $lightgray;
  @include media-breakpoint-up(md) {
    padding-bottom: 100px; }
  &__item {
    overflow: hidden; }
  .insta-logo {
    width: 132px;
    height: 38px;
    @include media-breakpoint-up(md) {
      width: 160px;
      height: 46px; }
    @include media-breakpoint-up(xxl) {
      width: 202px;
      height: 58px; } }
  &__wrapper {
    position: relative;
    @include media-breakpoint-down(md) {
      padding-left: 24px; } }
  &__slider {}
  &__img {
    display: block;
    @include scale(#{$_}__item); }
  &__button {
    &::before {
      content: none; } } }

.b-insta-info {
  display: flex;
  align-items: center;
  &__icon {
    flex: none;
    margin-right: 26px;
    svg {
      @include wh(70px);
      @include media-breakpoint-down(md) {
        @include wh(56px); } } }
  &__title {
    text-decoration: none;
    font-size: 22px;
    color: $body-color;
    transition: $transition; }
  &__text {
    display: flex;
    font-size: 13px;
    color: #bfbfbf;
    span {
      margin-right: 6px; }
    .num {
      font-weight: 700;
      color: $body-color; } } }
