.block-colors {
  margin-bottom: 50px;
  @include media-breakpoint-up(xl) {
    margin-bottom: 80px; }
  &__title {
    margin-bottom: 30px;
    font-size: 16px;
    span {
      position: relative;
      bottom: 4px;
      margin-left: 6px;
      font-size: 10px; } }
  &__list {
    display: flex;
    flex-wrap: wrap; }
  &__item {
    position: relative;
    width: calc(100%/3);
    margin-bottom: 10px;
    @include media-breakpoint-up(md) {
      width: calc(100%/5);
      margin-bottom: 20px; }
    @include media-breakpoint-up(xl) {
      width: calc(100%/8); } }
  &__color {
    position: absolute;
    top: 5px;
    right: 5px;
    @include wh(12px);
    border-radius: 50%;
    @include media-breakpoint-up(xxl) {
      @include wh(16px); }
    &.is-white {
      border: 2px solid #d9d9d9; } } }

