.modal {
  @include scrollbar;
  $_: &;
  &-close {
    position: absolute;
    z-index: 9;
    right: 0;
    top: 0;
    background-color: $body-color;
    background-image: url("~images/close.svg?fill=%23fff");
    background-repeat: no-repeat;
    background-position: center;
    @include wh(30px);
    @include media-breakpoint-up(xl) {
      @include wh(60px); }
    @include media-breakpoint-up(xxl) {
      @include wh(72px); }

