.modal-xs {
  max-width: 320px; }

.modal-header {
  &_border {
    border-bottom: 1px solid #bfbfbf; }
  @include media-breakpoint-down(md) {
    font-size: 16px;
    padding-top: 29px !important;
    padding-bottom: 29px !important;
    .modal-title {
      font-size: 16px !important; }
    .modal-close2 {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center; } } }


.modal-footer {
  padding: 0;
  display: block;
  border-top: 1px solid #D9D9D9;
  .btn {
    flex-grow: 1; } }

.modal-fullscreen {
  .modal-header {
    padding: 0; }
  .modal-body {
    padding: 0; } }


#modal-product {
  transition: 0.3s;
  &.is-loading {
    opacity: 0; } }


.is-product-modal-open {
  .modal-backdrop {
    background-color: #fff;
    &.show {
      opacity: 0.8; } } }

#mainMenuMobileModal {
  .modal-header {
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
    height: 56px;
    padding: 0 $container-offsets-mobile; }
  .modal-body {
    padding-top: 0; } }

#favoritesFilterModal {
  .modal-header {
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
    height: 56px;
    padding: 0 $container-offsets-mobile; }
  .modal-title {
    font-size: 16px; }
  .modal-body {
    padding-top: 0; } }

.modal-default {
  .modal-xs {
    .modal-header, .modal-body {
      padding-left: $container-offsets-mobile;
      padding-right: $container-offsets-mobile; } }
  .modal-sm {
    @include media-breakpoint-down(sm) {
      .modal-header, .modal-body {
        padding-left: $container-offsets-mobile;
        padding-right: $container-offsets-mobile; } } }
  .modal-md {
    @include media-breakpoint-down(sm) {
      .modal-header, .modal-body {
        padding-left: $container-offsets-mobile;
        padding-right: $container-offsets-mobile; } } }
  .modal-lg {
    .modal-header {
      @include make-container-offset; }
    .modal-body {
      @include make-container-offset; } } }

.modal-default {
  .modal-dialog {
    .modal-header {
      align-items: center;
      height: 56px;
      padding-top: 40px;
      padding-bottom: 40px; }
    .modal-title {
      font-size: 18px; } } }

#modal-order-list {
  .modal-header {
    border-bottom: 0; }
  .modal-body {
    padding-top: 0; } }

.modal-product-added {
  .modal-dialog {
    max-width: 600px; }
  .modal-footer {
    flex-wrap: nowrap;
    .btn {
      padding: 14px 34px;
      line-height: 1.1; } }
  .modal-body {
    padding: 40px 40px 60px 10px !important; }
  .order-list {
    &__item {
      border: none;
      padding: 0; } }
  @include media-breakpoint-down(md) {
    .modal-body {
      padding: 20px 10px 30px 10px !important; } } }

