.b-lookbook {
  //--var()
  $_: &;
  .grid-sizer, .grid-item {
    @include media-breakpoint-up(xl) {
      width: calc(100%/3 - 1%); } }
  .gutter-sizer {
    width: 1%; }
  .grid-item {}
  .grid-item--height2 {
    #{$_}__inner {
      padding-top: calc(56.25% * 2 + 120px); } }
  &__inner {
    position: relative;
    padding-top: calc(56.25% + 60px); }
  &__content {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 0;
    @include wh(100%); }
  &__img {
    height: calc(100% - 60px);
    img {
      display: block;
      @include wh(100%);
      object-fit: cover; } }
  &__title {
    flex: none;
    display: block;
    height: 60px;
    padding-top: 14px;
    font-size: 16px; } }

