.commerce-info {
	display: flex;
	flex-wrap: wrap;
	background: #fff;
	margin-bottom: 90px;
	align-items: flex-start;

	&__interior-slider-wrap {
		max-width: 50%;
		min-width: 50%;
		position: relative;
		margin-bottom: 100px; }
	&__interior-slider {
		img {
			height: 450px;
			object-fit: cover;
			width: 100%; }
		.swiper-pagination {
			display: none;
			width: auto;
			margin: 0 24px;
			@include media-breakpoint-down(lg) {
				display: block; } }
		.swiper-pagination-bullet {
			background: #fff; } }
	&__interior-thumbs {
		margin: 0 90px;
		height: 90px;
		position: static;
		&.no-arrows {
			margin: 0 auto;
			.swiper-navigation {
				display: none; } } }
	&__thumb {
		height: 90px;
		object-fit: cover;
		img {
			height: 90px;
			object-fit: cover; } }
	&__img {
		width: 100%;
		display: block;
		img {
			width: 100%; } }
	&__assorment {
		width: 100%;
		max-width: 100%;
		padding: 0 72px;
		margin-bottom: 40px;
		.section-header__title {
			font-size: 22px;
			font-weight: 500;
			line-height: 1.15; }
		.swiper-button-prev, .swiper-button-next {
			width: 50px !important;
			height: 50px !important;
			background: $lightgray !important;
			&.swiper-button-disabled {
				opacity: 0; }
			&:hover {
				background: $body-color !important; } }
		.swiper-pagination-bullet {
			margin: 0;
			&:only-child {
				display: none; } }
		.b-items__slider {
			margin: 0 -40px; }
		.section-header {
			padding-bottom: 4px; }
		.itemPlate__title {
			text-transform: uppercase;
			font-weight: 700;
			font-size: 14px;
			line-height: 1.2;
			span {
				margin-left: 4px;
				display: inline-block;
				vertical-align: super;
				font-size: 10px; } } }

	&__content {
		min-width: 50%;
		max-width: 50%;
		position: relative; }
	&__social {
		margin-bottom: 25px; }
	.social__video-link {
		margin-left: 20px;
		&:hover {
			color: #666 !important; } }
	.swiper-button-prev, .swiper-button-next {
		width: 90px;
		height: 90px;
		background: $body-color;
		&:after {
			content: '';
			width: 6px;
			height: 8px;
			clip-path: polygon(0 0, 0% 100%, 100% 50%);
			background-color: #fff;
			transition: 0.3s linear; } }
	.swiper-button-prev {
		left: 0;
		bottom: 0;
		margin-top: 0;
		top: unset;
		&:after {
			transform: scale(-1); } }
	.swiper-button-next {
		right: 0;
		bottom: 0;
		margin-top: 0;
		top: unset; }

	.info-card {
		padding: 60px 110px;
		background: #fff;
		&__metroItem {
			display: flex;
			align-items: center; }
		&__title {
			margin-top: 33px;
			font-weight: 500;
			font-size: 28px; }
		&__inner {
			max-width: unset;
			&:after {
				display: none; } }
		&__address {
			font-weight: 500;
			font-size: 16px; }
		&__d-address {
			font-size: 16px;
			font-weight: 500;
			line-height: 1.25;
			margin-bottom: 5px;
			.info-card__metro + & {
				margin-top: -4px; } }
		&__delivery {
			font-size: 16px;
			font-weight: 500;
			margin-bottom: 8px; }
		&__delivery-title {
			color: $gray-800;
			font-size: 16px;
			font-weight: 400; }
		&__point {
			display: flex;
			margin-bottom: 10px; }
		&__point-logo {
			width: 30px;
			height: 30px;
			margin-right: 8px;
			img {
				width: 30px;
				height: 30px;
				object-fit: contain; } }
		&__point-info {
			font-size: 13px;
			font-weight: 400;
			line-height: 1.4;
			padding-top: 3px; }
		&__payinfo {
			display: flex;
			align-items: center;
			margin-bottom: 18px; }
		&__payinfo-icon {
			width: 30px;
			height: 30px;
			margin-right: 8px; }
		&__payinfo-text {
			margin: 0;
			font-size: 12px;
			font-weight: 400;
			max-width: 220px;
			line-height: 1.3;
			color: $gray-800; }
		&__choose-btn {
			width: 110px; }
		&__contact {
			font-weight: 500;
			font-size: 16px; }
		&__days {
			font-weight: 500;
			font-size: 16px;
			span {
				font-size: 16px; }
			br {
				display: none; } }
		&__labels {
			position: absolute;
			display: flex;
			top: 0;
			right: 0; }
		&__partner {
			position: static;
			font-weight: 700;
			font-size: 10px;
			padding: 10px 20px;
			white-space: nowrap;
			svg {
				margin-right: 10px; } }
		&__yandex-choice {
			background: #FFD600;
			height: 40px;
			display: flex;
			align-items: center;
			justify-content: center;
			color: $body-color;
			text-transform: uppercase;
			font-size: 10px;
			padding: 10px 20px;
			font-weight: 700;
			svg {
				width: 18px;
				height: 18px;
				margin-right: 10px; } }
		&__how-get {
			align-self: flex-start;
			width: 130px;
			margin-top: 55px;
			div {
				margin-bottom: 20px; }
			&:hover {
				background: $gray-800;
				color: #fff;
				border: 1px solid $gray-800; } } }

	&_on-map {
		.info-card {
			padding: 0;
			&__title {
				font-size: 15px;
				font-weight: 500;
				margin-top: 16px;
				margin-bottom: 6px; }
			&__metro {
				margin-bottom: 12px; }
			&__address {
				font-size: 14px;
				font-weight: 400;
				margin-bottom: 10px;
				line-height: 1.6; }
			&__contact {
				font-size: 14px;
				font-weight: 400;
				margin-bottom: 15px;
				padding-top: 0;
				line-height: 1.6; }
			&__social {
				margin-bottom: 16px; }
			&__days {
				font-size: 14px;
				font-weight: 400;
				margin-bottom: 30px; }
			&__how-get {
				width: 100%;
				margin-top: 0;
				&:hover {
					background: #fff;
					border: #fff;
					color: $body-color; }
				p {
					margin-bottom: 10px;
					font-size: 14px;
					font-weight: 400; } } } }
	&_ymaps.commerce-info {
		padding: 0 30px;
		.info-card {
			&__cover {
				margin: 0 -30px; }
			&__title {
				margin-top: 15px;
				font-weight: 500; } } }
	&_delivery.commerce-info {
		padding: 30px; }

	&_preview {
		margin-bottom: 0;
		padding: 25px 40px;
		border-bottom: 1px solid #d9d9d9;
		transition: 0.3s;
		cursor: pointer;
		&.active {
			background: $lightgray; }
		.info-card {
			background: none;
			&__days {
				margin-bottom: 0;
				font-size: 13px; }
			&__address {
				margin-bottom: 5px;
				font-size: 13px; }
			&__contact {
				margin-bottom: 5px;
				font-size: 13px; } } }

	@media (max-width: 1439px) {
		margin-bottom: 40px;
		&__assorment {
			padding: 0 40px;
			margin-bottom: 20px;
			.b-items__slider {
				margin: 0 -20px; }
			.section-header__title {
				font-size: 20px; } }
		&__interior-slider-wrap {
			margin-bottom: 100px; }
		&__interior-thumbs {
			margin: 0 60px;
			height: 60px; }
		&__thumb {
			height: 60px;
			img {
				height: 60px; } }
		.swiper-button-prev, .swiper-button-next {
			width: 60px;
			height: 60px; }

		.info-card {
			padding: 60px 100px;
			&__title {
				font-size: 18px;
				margin-top: 25px; } }

		&_on-map {
			.info-card {
				padding: 0; } }

		&_preview {
			margin-bottom: 0; } }

	@media (min-width: 1440px) {
		.itemPlate {
			padding: 36px 40px 20px; } }
	@include media-breakpoint-down(xl) {
		.info-card {
			padding: 60px 30px 30px; }
		&__interior-slider-wrap {
			margin-bottom: 40px; }
		&_on-map {
			.info-card {
				padding: 0; } } }
	@include media-breakpoint-down(lg) {
		&__interior-slider-wrap {
			max-width: 100%;
			width: 100%;
			margin-bottom: 0; }
		&__interior-thumbs {
			display: none; }
		&__content {
			max-width: 100%;
			width: 100%; }
		&__assorment {
			position: relative;
			margin-bottom: 30px;
			.swiper-pagination-bullet {
				margin: 0 3px;
				&:first-child {
					margin-left: 0; } }
			.section-header__navi {
				position: absolute;
				bottom: 0;
				left: 24px; } }

		.info-card {
			padding: 0 24px 30px;
			&__yandex-choice {
				flex-grow: 1;
				span {
					display: none; } }
			&__partner {
				flex-grow: 1; }
			&__labels {
				position: static;
				margin: 0 -24px 30px; } }

		&_delivery.commerce-info {
			padding: 24px;
			.info-card {
				padding: 0; } }
		&_ymaps.commerce-info {
			padding: 0;
			.info-card {
				padding: 20px;
				//      (  iOS)
				//&__choose-btn
				//	position: fixed
				//	bottom: 0
				//	left: 0
				//	width: 100vw
				//	height: 54px
				//	border-radius: 0 !important
				//	z-index: 11
				//	font-size: 14px
				//	font-weight: 500
				//&__payinfo
 } } }				//	margin-bottom: 0
	@include media-breakpoint-down(md) {
		margin-bottom: 0;
		padding-bottom: 60px;
		&:last-of-type {
			padding-bottom: 30px; }
		&__social {
			flex-wrap: wrap;
			.social__video-link {
				width: 100%;
				margin-left: 0;
				margin-top: 14px; } }
		&__assorment {
			padding: 0 24px;
			margin-bottom: 0;
			.section-header__title {
				font-size: 18px; }
			.itemPlate__title {
				font-size: 12px; } }
		&__interior-slider {
			img {
				height: 195px; } }

		.info-card {
			&__title {
				font-size: 18px;
				margin-top: 14px;
				margin-bottom: 10px;
				line-height: 1.2;
				br {
					display: none; } }
			&__d-address {
				font-size: 15px; }
			&__delivery {
				font-size: 15px; }
			&__address {
				font-size: 14px;
				margin-bottom: 12px; }
			&__contact {
				font-size: 14px;
				padding-top: 0;
				margin-bottom: 22px; }
			&__days {
				font-size: 14px; }
			&__labels {
				justify-content: flex-end; }
			&__partner {
				font-size: 10px;
				max-width: 50%;
				padding: 10px 15px; }
			&__yandex-choice {
				font-size: 10px;
				max-width: 50%;
				padding: 10px 15px; }
			&__how-get {
				margin-top: 25px; } }

		&_on-map {
			.info-card {
				&__how-get {
					margin-top: 0; }
				&__title {
					font-size: 16px;
					margin-top: 0;
					margin-bottom: 10px;
					font-weight: 500;
					line-height: 1.4; }
				&__address {
					font-size: 12px;
					line-height: 1.4; }
				&__contact {
					font-size: 12px;
					margin-bottom: 15px;
					line-height: 1.4; }
				&__days {
					font-size: 12px; } } }
		&__social {
			margin-bottom: 15px; } }

	.ymaps-2-1-78-balloon__content & {
		margin-bottom: 0;
		@include media-breakpoint-down(lg) {
			padding-bottom: 0; }
		.info-card {
			&__cover {
				max-height: 170px;
				img {
					max-height: 170px;
					width: 100%;
					object-fit: cover;
					object-position: top; } }
			&__days {
				margin-bottom: 20px;
				font-size: 13px;
				line-height: 1.4; }
			&__address {
				font-size: 13px;
				line-height: 1.4; }
			&__contact {
				font-size: 13px;
				line-height: 1.4; } } } }

