.b-catalog {
  $_: &;
  --padding-top: 70px;
  @include media-breakpoint-up(md) {
    --padding-top: 76px; }
  @include media-breakpoint-up(xxl) {
    --padding-top: 90px; }
  background-color: #fff;
  &__list {
    @include media-breakpoint-only(md) {
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          padding-left: $container-offset;
          padding-right: $container-offset; } } } }
  &__item {
    display: flex;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    padding-top: var(--padding-top);
    @include media-breakpoint-down(md) {
      align-items: center;
      min-height: 90px;
      border-bottom: 1px solid $lightgray;
      padding-top: 0;
      &:first-child {
        border-top: 1px solid $lightgray; }
      &:nth-child(n+5) {
        display: none; } }
    @include media-breakpoint-up(md) {
      flex-direction: column; } }
  &__title {
    @include panel-title;
    #{$_}__content & {
      color: #fff; } }
  &__img {
    margin-left: auto;
    margin-top: auto;
    img {
      @include media-breakpoint-down(md) {
        width: 96px; } } }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1; }
  &__content {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    color: #fff;
    background-color: #fa002a;
    overflow: hidden;
    transform: translateY(-100%);
    transition: $transition-elems;
    z-index: 1;
    @include media-breakpoint-down(md) {
      display: none; }
    #{$_}__item:hover & {
      transform: translateY(0); }
    a {
      font-weight: 400;
      text-decoration: none;
      color: inherit !important; } }
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: var(--padding-top);
    padding-bottom: 90px;
    transform: translateY(100%);
    transition: $transition-elems;
    @include media-breakpoint-down(xxl) {
      padding-bottom: 30px; }
    #{$_}__item:hover & {
      transform: translateY(0); } }
  &__category {
    margin-bottom: 26px;
    font-size: 20px; }
  &__listChildren {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 14px;
    @include media-breakpoint-up(xxl) {
      font-size: 16px; }
    a {
      &:nth-child(n+11) {
        display: none; }
      &:hover {
        text-decoration: underline; } } }
  &__footer {
    @include media-breakpoint-down(sm) {
      display: flex;
      align-items: center;
      height: 90px;
      padding-bottom: 0;
      padding-top: 0; } }
  &__contentButton {
    margin-top: auto;
    @include media-breakpoint-down(xl) {
      font-size: 15px; }
    span {
      margin-left: 10px;
      display: inline-block;
      vertical-align: super;
      font-size: 12px;
      font-weight: 400; } } }
