.order-list {
  &__item {
    display: flex;
    padding: 20px 0 20px;
    border-top: 1px solid #D9D9D9;
    @include media-breakpoint-up(md) {
      padding-bottom: 6px; } }
  &__img {
    width: 68px;
    margin-right: 10px;
    flex: none;
    @include media-breakpoint-up(md) {
      width: 120px;
      margin-right: 20px; }
    img {
      width: 100%; } }
  &__content {
    @include media-breakpoint-up(md) {
      display: flex;
      flex: 1; } }
  &__inner {
    @include media-breakpoint-up(md) {
      max-width: 280px; } }
  &__title {
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1.15; }
  &__counter {
    margin-bottom: 10px;
    @include media-breakpoint-up(md) {
      width: 80px;
      flex: none;
      margin-left: auto; }
    span {
      font-weight: 400; } }
  &__price {
    @include media-breakpoint-up(md) {
      width: 100px;
      margin-left: 70px;
      flex: none;
      text-align: right; } }
  &__priceNew {
    font-size: 16px; }
  &__discount {
    font-size: 12px;
    color: #fa002a;
    letter-spacing: -0.025em;
    display: inline-block;
    margin-right: 6px; }
  &__priceOld {
    letter-spacing: -0.025em;
    font-size: 12px;
    display: inline-block;
    text-decoration: line-through; } }
