.b-filter {
  height: 100%;
  position: relative;
  .form-check {
    font-size: 16px;
    @include media-breakpoint-up(xxl) {
      font-size: 16px; } }
  &__content {
    @include media-breakpoint-down(xl) {
      height: 100%;
      overflow: auto;
      padding-bottom: 100px;
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          padding-left: $container-offset;
          padding-right: $container-offset; } } } }
  &__footer {
    display: flex;
    z-index: 3;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        left: $container-offset * -1;
        right: $container-offset * -1; } }
    @include media-breakpoint-up(xl) {
      margin-top: 50px; }
    @include media-breakpoint-up(xxl) {
      margin-top: 60px; }
    @include media-breakpoint-down(xl) {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      .mob-none {
        display: none; } }
    .btn {
      flex: 1;
      display: block;
      padding-top: 20px;
      padding-bottom: 20px;
      font-size: 12px;
      letter-spacing: -0.07px;
      @include media-breakpoint-up(xl) {
        font-size: 14px; } } }
  &__clear-all {
    &:hover {
      background: $body-color;
      color: #fff;
      svg {
        fill: #fff; } } }
  &__submit {
    @include media-breakpoint-up(xl) {
      display: none !important; } }
  .nav-cats {
    a {
      transition: 0.3s;
      &:hover, &:focus {
        color: $gray; }
      &.is-active {
        color: $gray; } } } }

.filter-item {
  padding-top: 60px;
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #d9d9d9;
    padding-bottom: 10px; }
  &__title {
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase; }
  &__clear {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #666666;
    font-weight: 400;
    cursor: pointer;
    &::before {
      content: '';
      margin-right: 5px;
      width: 16px;
      height: 16px;
      background: $body-color url("~images/close_small.svg?fill=%23fff") 50% 50% no-repeat;
      border-radius: 50%;
      transition: 0.3s; }
    &:hover {
      &:before {
        background-color: $gray; } } }
  &__content {
    padding-top: 40px; }
  &__box {
    display: flex;
    align-items: center;
    min-height: 70px;
    background-color: #ffffff;
    padding: 0 24px;
    margin-bottom: 30px;
    @include media-breakpoint-up(xxl) {
      padding: 0 30px; } } }


.filter-range-prices {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #999;
  font-size: 12px;
  font-weight: 400;
  vertical-align: top;
  @include media-breakpoint-up(xxl) {
    font-size: 14px; }
  &__item {
    display: flex; }
  &__price {
    margin-left: 11px;
    font-size: 14px;
    font-weight: 500;
    color: $body-color;
    span {
      padding-right: 6px; }
    @include media-breakpoint-up(xxl) {
      margin-left: 32px;
      font-size: 20px; } } }
