.stars-select {
  display: flex;
  align-items: center;
  &__item {
    margin-right: 1px;
    cursor: pointer;
    transition: 0.3s;
    svg {
      transition: 0.3s; }
    &.selected, &:hover {
      svg {
        fill: #ECC400; } } }
  @include media-breakpoint-down(lg) {
    &__item {
      svg {
        width: 26px;
        height: 26px;
        margin-right: 5px; } } } }
