.form-control {
  &_2 {
    padding-left: 0;
    border-left: 0;
    border-top: 0;
    border-right: 0;
    background-color: transparent !important;
    box-shadow: none !important;
    font-size: 18px; } }


.help-text {
  margin-top: 6px;
  color: #999;
  font-size: 14px; }

.form-group-kod {
  flex: none;
  width: 57px;
  position: relative;
  margin-right: 10px;
  &::before {
    content: '';
    display: block;
    font-size: 14px;
    margin-bottom: 4px;
    //position: absolute
    //left: 0
 }    //top: -10px
  input {
    color: #1F1F1F; } }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; }

.flatpickr-calendar {
  margin-bottom: 60px;
  box-shadow: none;
  width: 380px;
  @include media-breakpoint-down(xl) {
    width: auto;
    margin-bottom: 40px;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        margin-left: $container-offset * -1;
        margin-right: $container-offset * -1; } } } }

.dayContainer {
  width: 100%;
  min-width: 0;
  max-width: 100%; }

.flatpickr-days {
  width: 100%;
  background-color: $lightgray; }

.flatpickr-weekdays {
  margin-top: 18px;
  margin-bottom: 18px; }


.flatpickr-prev-month {
  position: static; }
.flatpickr-months {
  position: relative;
  width: 230px;
  margin: 0 auto;
  @include media-breakpoint-up(xl) {
    margin: 0; }
  .flatpickr-prev-month, .flatpickr-next-month {
    padding-top: 0;
    padding-bottom: 0;
    position: static;
    display: flex;
    align-items: center; }
  input.cur-year {
    font-weight: 500; } }
.flatpickr-current-month {
  display: flex;
  align-items: center;
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  .flatpickr-monthDropdown-months {
    font-weight: 500;
    font-size: 16px;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: ''; } }

span.flatpickr-weekday {
  font-size: 14px;
  font-weight: 400; }

.flatpickr-day {
  height: 40px;
  line-height: 40px;
  border-radius: 0;
  width: calc(100% / 7);
  max-width: none;
  display: block;
  font-weight: 500;
  @include media-breakpoint-up(xl) {
    height: 48px;
    line-height: 48px; }
  &.today {
    border: 0;
    background-color: $body-color;
    color: #fff; }
  &.selected {
    border: 1px solid $body-color;
    background-color: transparent;
    color: $body-color; } }

.form-check {
  cursor: pointer;
  label {
    color: $gray-800;
    cursor: pointer; }
  &:hover {
    label {
      color: $body-color; } } }

.form-check-input {
  cursor: pointer;
  -webkit-appearance: none;
  &:focus {
    box-shadow: none;
    border-color: $body-color; } }

.form-switch {
  .form-check-input {
    cursor: pointer;
    -webkit-appearance: none;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 0.9%29'/%3e%3c/svg%3e") !important;
    &:focus {
      box-shadow: none;
      border-color: $body-color; }
    &:checked {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e") !important; } } }

.form-order-switch {
  display: flex;
  align-items: center;
  [data-price] {
    cursor: pointer;
    color: #999;
    &.is-active {
      color: $body-color; } }
  .form-switch {
    display: flex;
    align-items: center;
    margin: 0 4px 0 10px;
    .form-check-input {
      margin-top: 0;
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23000'/%3e%3c/svg%3e") !important;
      background-color: transparent !important;
      border-color: #ccc !important; } } }

