.b-about {
  overflow: hidden;
  &__inner {
    margin-top: -100px;
    padding-top: 100px;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    background-color: #080808;
    p {
      color: #fefefe; }
    a {
      color: inherit; }
    @include media-breakpoint-down(lg) {
      padding-bottom: 120px; }
    @include media-breakpoint-up(lg) {
      margin-top: -380px;
      padding-top: 380px;
      font-size: 16px; } }
  &__logo {
    position: relative;
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
    @include wh(79px);
    @include media-breakpoint-up(lg) {
      @include wh(120px);
      transform: translateY(-50%); }
    @include media-breakpoint-up(xxl) {
      @include wh(159px); }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 82px; }
    .icon-round-logo {
      position: absolute;
      width: 19px;
      height: 16px;
      left: 55%;
      top: 50%;
      //margin-left: 10px
      transform: translate(-50%, -50%);
      fill: #4d4d4d;
      @include media-breakpoint-up(xl) {
        width: 28px;
        height: 24px; }
      @include media-breakpoint-up(xl) {
        width: 38px;
        height: 32px; } }
    .icon-round-text {
      display: block;
      @include wh(100%);
      fill: #4d4d4d;
      animation: spin 20s infinite linear; } }
  &__topText {
    margin-bottom: 30px;
    margin-top: 40px;
    @include media-breakpoint-up(lg) {
      margin-top: 122px;
      margin-bottom: 150px; }
    @include media-breakpoint-up(xxl) {
      margin-top: 150px;
      margin-bottom: 150px; }
    @include media-breakpoint-down(md) {
      font-size: 17px;
      letter-spacing: -0.015em;
      line-height: 1.35; } }
  &__button {
    margin-top: 50px;
    @include media-breakpoint-up(xxl) {
      margin-top: 242px; } }
  &__img {
    display: block;
    &_1 {
      @include media-breakpoint-down(lg) {
        margin-bottom: 60px; } }
    &_2 {
      @include media-breakpoint-down(lg) {
        margin-bottom: 40px; }
      @include media-breakpoint-up(lg) {
 } } } }        //margin-top: -150px
