.shops-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 58px;
  margin-bottom: 30px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  @include media-breakpoint-down(lg) {
    border-top: 0;
    border-bottom: 0;
    height: auto;
    margin-top: 5px; }
  &__mobile {
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    .btn {
      margin: 0 5px; }
    @include media-breakpoint-down(lg) {
      display: flex; } }
  &__cities-nav {
    display: flex;
    justify-content: space-between;
    width: 50%;
    align-items: center;
    @include media-breakpoint-down(lg) {
      display: none; } }
  &__cities-nav-list {
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: -1px; }
  &__cities-item {
    list-style: none;
    margin-right: 20px;
    height: 57px;
    display: block; }
  &__cities-link {
    padding: 19px 0;
    color: $gray-800;
    font-weight: 400;
    font-size: 14px;
    border-bottom: 1px solid #d9d9d9;
    display: block;
    transition: 0.3s;
    height: 57px;
    .active &, &:hover {
      color: $body-color;
      border-bottom: 1px solid $body-color;
      font-weight: 500; } }
  &__interactive {
    display: flex;
    justify-content: space-between;
    padding-left: 110px;
    flex-grow: 1;
    align-items: center;
    @include media-breakpoint-down(lg) {
      display: none; } }
  &__nearest {
    svg {
      width: 15px;
      height: 15px; }
    &:hover {
      background: $gray-800;
      color: #fff;
      border: 1px solid $gray-800; } }
  .form-switch_double {
    display: flex;
    align-items: center;
    .form-check-label {
      font-weight: 500;
      color: $gray;
      &:first-of-type {
        order: 1;
        color: $body-color; }
      &:last-of-type {
        order: 3; } }
    .form-check-input {
      order: 2;
      position: static;
      margin: 0 7px;
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba%280, 0, 0, 1'/%3e%3c/svg%3e") !important;
      -webkit-appearance: none;
      &:checked {
        background-color: #fff;
        border: 2px solid #cccccc;
        & ~.form-check-label {
          &:first-of-type {
            color: $gray; }
          &:last-of-type {
            color: $body-color; } } }
      &:focus {
        border: 2px solid #cccccc; } } }
		.shops-header__how-get {
			&:hover {
				background: $gray-800;
				color: #fff;
				border: 1px solid $gray-800; } }
  @media (max-width: 1439px) {
    &__cities-item {
      &:nth-child(n+6) {
        display: none; } }
    &__interactive {
      padding-left: 100px; } }
  @include media-breakpoint-down(xl) {
    &__cities-item {
      &:nth-child(n+5) {
        display: none; } }
    &__interactive {
      padding-left: 30px; } } }
