.footer-top {
  background-color: #fff;
  padding-top: 135px;
  font-weight: 400;
  color: #666;
  line-height: 1.6;
  @include media-breakpoint-up(xxl) {
    padding-top: 160px; }
  @include media-breakpoint-up(md) {
    padding-bottom: 78px; }
  &__logo {
    display: flex;
    align-items: flex-end;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 22px;
    line-height: .6;
    color: $body-color !important;
    text-decoration: none;
    @include media-breakpoint-up(xl) {
      margin-bottom: 56px; }
    @include media-breakpoint-down(sm) {
      margin-top: 20px; }
    svg {
      width: 31px;
      height: 26px;
      margin-right: 6px; } }
  &__smitem {
    margin-bottom: 13px; }
  &__smitemTitle {
    margin-bottom: 8px;
    font-weight: 500;
    color: $body-color;
    @include media-breakpoint-down(sm) {
      margin-bottom: 10px; } }
  &__social-phone {
    display: flex;
    align-items: center;
    margin-bottom: 24px; }
  &__phone {
    margin-left: 15px;
    font-weight: 500;
    color: $body-color !important;
    text-decoration: none; }
  &__social {
    display: flex;
    margin-top: -4px;
    @include media-breakpoint-up(xl) {
      justify-content: flex-end; }
    @include media-breakpoint-down(lg) {
      margin-top: 30px; }
    @include media-breakpoint-down(md) {
      margin-top: 0;
      margin-bottom: 40px; } }
  &__nav {
    @include media-breakpoint-down(md) {
      margin-bottom: 30px; } }
  &__navTitle {
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 500;
    color: $body-color;
    @include media-breakpoint-up(md) {
      min-height: 33px;
      margin-bottom: 20px;
      font-size: 22px; }
    @include media-breakpoint-up(lg) {
      margin-bottom: 47px; }
    &:empty {
      @include media-breakpoint-down(xxl) {
        display: none; } }
    &_social {
      font-size: 14px;
      @include media-breakpoint-up(lg) {
        display: none; } } } }

.nav-footer {
  $_: &;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  line-height: 21px;
  @include media-breakpoint-up(md) {
    line-height: normal; }
  &__item {
    @include link-gray;
    display: block;
    text-decoration: none;
    margin-bottom: 8px;
    #{$_}_1 &:nth-child(3) {
      margin-bottom: 10px;
      @include media-breakpoint-up(lg) {
        margin-bottom: 30px; } } }
  &__toggle {
    display: inline-block;
    margin-top: 12px;
    cursor: pointer;
    line-height: 13px;
    border-bottom: 1px dashed currentColor;
    @include media-breakpoint-up(sm) {
      display: none; }
    #{$_}.is-active & {
      opacity: 0; }
    & ~ * {
      @include media-breakpoint-down(sm) {
        display: none; }
      #{$_}.is-active & {
        display: block; } } } }

.footer-middle {
  background-color: #fff;
  @include media-breakpoint-down(xl) {
    .row {
      flex-wrap: wrap;
      & > * {
        width: 50%; }
      & > *:last-child {
        width: 100%;
        margin-top: 20px; } } }
  @include media-breakpoint-down(md) {
    .row {
      flex-direction: column;
      & > * {
        width: 100%; }
      & > *:last-child {
        margin-top: 0; } } }
  &__contact {
    @include media-breakpoint-down(md) {
      padding-bottom: 55px; } }
  &__pays {
    font-size: 12px;
    color: #999;
    img {
      display: block;
      vertical-align: middle;
      margin-top: 10px;
      @media (min-width: 1920px) {
        display: inline-block;
        margin-left: 15px;
        margin-top: 0; } } } }

.footer-bottom {
  padding-bottom: 50px;
  background-color: #fff;
  &::before {
    content: '';
    display: block;
    height: 90px;
    margin-bottom: 36px;
    border-bottom: 1px solid #e6e6e6; }
  @include media-breakpoint-down(sm) {
    padding-top: 20px;
    font-size: 13px;
    &::before {
      height: 60px; } }
  &__copy {
    color: #999;
    @include media-breakpoint-down(md) {
      margin-bottom: 30px; } }
  &__politic {
    color: $body-color;
    @include media-breakpoint-down(md) {
      margin-bottom: 30px; }
    @include media-breakpoint-up(xl) {
      display: flex;
      div {
        margin-left: 20px;
        &:first-child {
          margin-left: 0; } } }
    a {
      color: inherit;
      text-decoration: none; } }
  &__develop {
    color: #999;
    @include media-breakpoint-up(xl) {
      display: flex;
      justify-content: flex-end;
      div {
        margin-left: 20px;
        &:first-child {
          margin-left: 0; } } }
    a {
      color: $body-color;
      text-decoration: none; } } }

