@import "../../sass/utilities/variables";

.b-labels {
  $_: &;
  display: flex;
  flex-direction: column-reverse;
  &__label {
    @include wh(38px);
    flex: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -6px;
    border-radius: 50%;
    color: #fff;
    font-weight: 700;
    font-size: 10px;
    letter-spacing: -0.3px;
    @include media-breakpoint-up(xxl) {
      @include wh(42px);
      font-size: 12px;
      #{$_}_product & {
        @include wh(50px); } }
    &:last-child {
      margin-top: 0; }
    &_new {
      background-color: #7bc537; }
    &_hit {
      background-color: #5c81e8; }
    &_discount {
      background-color: #fa002a; } } }

