.productList {
  padding-top: 36px;
  padding-bottom: 46px;
  @include media-breakpoint-up(xl) {
    padding-top: 100px;
    padding-bottom: 80px; }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px; }
  &__title {
    margin: 0;
    //font-size: 18px
    //+media-breakpoint-up(xl)
 }    //  font-size: 22px
  &__navi {
    display: flex;
    align-items: center;
    .swiper-navigation {
      @include swiper-navigation;
      display: flex;
      --swiper-navigation-size: #{rem(50px)};
      @include media-breakpoint-up(xl) {
        --swiper-navigation-size: #{rem(50px)}; }
      @include media-breakpoint-up(xxl) {
        --swiper-navigation-size: #{rem(50px)}; }
      @include media-breakpoint-down(xl) {
        display: none; } }
    .swiper-pagination {
      @include media-breakpoint-up(xl) {
        @include pagination; } } } }

.productListSlider {
  @each $breakpoint, $container-offset in $container-offsets {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      margin-right: $container-offset * -1; } }
  .swiper-slide {
    height: auto;
    display: flex; }
  .swiper-navigation {
    @include media-breakpoint-down(xl) {
      display: none; } } }

.productListItem {
  --img-width: 78px;
  @include media-breakpoint-up(xl) {
    --img-width: 120px; }
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin-right: 24px;
  @include media-breakpoint-up(xl) {
    margin-right: 75px; }
  @include media-breakpoint-down(xl) {
    margin-bottom: 37px;
    padding-bottom: 50px; }

  &__img {
    flex: none;
    width: var(--img-width);
    margin-right: 10px;
    @include media-breakpoint-up(xl) {
      height: 100px; }
    img {
      object-fit: contain;
      width: 100%; } }
  &__content {
    flex: 1;
    display: flex;
    flex-direction: column; }
  &__title {
    display: block;
    font-size: 13px;
    line-height: 1.2;
    margin-bottom: 10px;
    @include media-breakpoint-up(xl) {
      font-size: 14px; } }
  &__footer {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    border-top: 1px solid #dedede;
    @include media-breakpoint-up(xl) {
      width: 100%;
 }      //transform: translateY(-100%)
    @include media-breakpoint-down(xl) {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin-top: 12px;
      border-top: 1px solid #dedede; } }
  &__price {
    line-height: 1.1;
    letter-spacing: -0.025em; }
  &__priceOld {
    font-size: 12px;
    text-decoration: line-through;
    color: #999; }
  &__priceNew {
    font-size: 16px; }
  &__btn {
    display: flex;
    align-items: center;
    svg {
      @include wh(11px);
      fill: #fff; }
    span {
      margin-left: 4px !important; } } }
