.itemMd {
  margin-bottom: 54px;
  padding-top: 30px;
  background-color: #fff;
  font-size: 12px;
  @include media-breakpoint-up(xxl) {
    font-size: 14px; }
  &__top {
    display: flex; }
  &__img {
    flex: none;
    width: 80px;
    margin-right: 10px;
    @include media-breakpoint-up(xxl) {
      margin-right: 12px;
      width: 118px; }
    img {
      width: 100%; } }
  &__content {
    flex: 1;
    margin-bottom: 10px;
    padding-top: 10px;
    padding-right: 16px;
    @include media-breakpoint-up(xxl) {
      padding-right: 60px; } }
  .itemMd__button_cart:hover {
    background: $dark-hover;
    color: #fff; }
  &__option {
    transition: 0.3s;
    &:not(.is-active):hover {
      background: $dark-hover;
      border-color: $dark-hover;
      svg {
        fill: #fff; } } }
  &__options {
    .itemMd__option {
      border-bottom: 1px solid #fff;
      &:last-child {
        border-right: 0; } } }
  &__stars {
    display: flex;
    margin-bottom: 4px;
    .svg-stars {
      margin-right: 10px; }
    span {
      font-size: 11px; } }
  &__title {
    margin-bottom: 10px;
    font-size: 13px;
    @include media-breakpoint-up(xxl) {
      font-size: 16px; } }
  &__sbl {
    margin-bottom: 6px;
    color: #999;
    font-size: 12px;
    span, a {
      display: inline-block;
      margin-left: 4px;
      color: $body-color; } }
  &__color {
    @include wh(12px);
    border-radius: 50%;
    display: inline-block;
    vertical-align: middle;
    margin-left: 6px; }
  @include itemFooter;
  --height: 50px;
  @include media-breakpoint-up(xxl) {
    --height: 60px; }
  &__footer {
    margin-left: 16px;
    margin-right: 16px;
    margin-bottom: 16px;
    padding-top: 14px;
    @include media-breakpoint-up(xxl) {
      margin-left: 40px;
      margin-right: 40px;
      margin-bottom: 30px; } } }

