.page-link {
  display: flex;
  align-items: center;
  justify-content: center;
  @include wh(60px);
  &__wrap {
    margin-right: 1px;
    border-right: 1px solid #F7F7F7;
    &:last-child {
      border-right: 0; } }
  &_arrow {
    font-size: 0;
    padding-bottom: 11px;
    width: 120px;
    @media (max-width: 1440px) {
      width: 60px; }
    span {
      display: flex;
      margin-top: 4px; } } }
.page-item.active {
  .page-link {
    background: $body-color;
    border-color: $body-color; } }
