.b-search {
  position: relative;
  flex: 1;
  .form-control {
    border: 0;
    border-bottom: 1px solid #d9d9d9;
    border-radius: 0;
    padding-left: 0;
    &:focus {
      box-shadow: none; }
    @include placeholder {
      color: #999999; } }
  span {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 27px;
    height: 28px;
    background: url("~images/search.svg") center / 16px 16px no-repeat;
    &:first-child {
      left: 7px; }
    &:last-child {
      right: 0; } }
  input {
    padding-right: 30px; }
  span + input {
    padding-left: 40px; } }

