@mixin up($size) {
  @media (min-width: $size) {
    @content; } }
@mixin down($size) {
  @media (max-width: $size) {
    @content; } }

@mixin make-container-offset($offsets: $container-offsets, $breakpoints: $grid-breakpoints) {
  @each $breakpoint, $container-offset in $offsets {
    @include media-breakpoint-up($breakpoint, $breakpoints) {
      padding-left: $container-offset;
      padding-right: $container-offset; } } }

@mixin wh($size: 100%) {
  width: $size;
  height: $size; }

@mixin center {
  display: flex;
  align-items: center;
  justify-content: center; }

@mixin placeholder {
  &::-webkit-input-placeholder {
    @content; }
  &:-moz-placeholder {
    @content; }
  &::-moz-placeholder {
    opacity: 1;
    @content; }
  &:-ms-input-placeholder {
    @content; } }

@mixin h1 {
  margin-bottom: 20px;
  font-size: 24px;
  letter-spacing: -0.025em;
  @include media-breakpoint-up(xl) {
    margin-bottom: 70px;
    font-size: 32px; }
  @include media-breakpoint-up(xxl) {
    margin-bottom: 80px;
    font-size: 40px; } }
@mixin h2 {
  font-size: 24px;
  letter-spacing: -0.025em;
  @include media-breakpoint-up(xl) {
    font-size: 26px;
    letter-spacing: -0.02em; }
  @include media-breakpoint-up(xxl) {
    font-size: 28px;
    letter-spacing: -0.025em; } }
@mixin h3 {
  font-size: 22px;
  @include media-breakpoint-up(xl) {
    font-size: 18px;
    letter-spacing: -0.025em; }
  @include media-breakpoint-up(xxl) {
    font-size: 22px;
    letter-spacing: -0.02em; } }

@mixin scrollbar {
  &::-webkit-scrollbar {
    width: 3px; }
  &::-webkit-scrollbar-track {
    background: transparent; }
  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: $body-color; }
  &::-webkit-scrollbar-thumb:hover {
    background: $body-color; } }

@mixin scale($el) {
  will-change: transform;
  transition: $transition-elems;
  #{$el}:hover & {
    transform: $zoom; } }

@mixin reset-input {
  border: 0;
  background: none;
  &:focus {
    outline: none; } }
@mixin reset-input-number {
  @include reset-input;
  &::-webkit-inner-spin-button,
  &::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; } }

