@mixin panel-title {
  position: relative;
  z-index: 1;
  font-size: 18px;
  font-weight: 500;
  transition: $transition;
  color: #1f1f1f;
  line-height: 1.1;
  @include media-breakpoint-up(sm) {
    margin-bottom: 25px;
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase; }
  @include media-breakpoint-up(xxl) {
    margin-bottom: 75px;
    font-size: 28px; }
  span {
    position: absolute;
    display: inline-block;
    margin-left: 10px;
    font-size: 10px;
    font-weight: 700;
    vertical-align: super;
    @include media-breakpoint-up(sm) {
      font-weight: 500;
      font-size: 14px; }
    @include media-breakpoint-down(md) {
      margin-top: -3px; } } }


@mixin itemFooter {
  --height: 60px;
  @include media-breakpoint-up(xxl) {
    --height: 80px; }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    margin-bottom: 36px;
    padding-top: 20px;
    border-top: 1px solid #d9d9d9; }
  &__oneclickbuy {
    border-radius: 100px; }
  &__price {
    display: flex;
    align-items: center; }
  &__priceOld {
    margin-left: 16px;
    text-decoration: line-through;
    font-size: 14px;
    color: #999;
    letter-spacing: -0.35px;
    @include media-breakpoint-down(md) {
      margin-right: 8px;
      font-size: 12px;
      letter-spacing: -0.3px; } }
  &__priceNew {
    font-size: 17px;
    letter-spacing: -0.42px;
    @include media-breakpoint-down(xl) {
      font-size: 13px;
      letter-spacing: -0.33px; } }
  &__targets {
    display: flex;
    height: var(--height); }
  &__button {
    flex: 1; }
  &__options {
    flex: 1;
    display: flex; }
  &__option {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #e6e6e6;
    cursor: pointer;
    &:last-child {
      border-left: 0; }
    svg {
      @include wh(14px);
      pointer-events: none; } }
  &__button {
    flex: 1;
    letter-spacing: -0.07px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: $body-color;
    cursor: pointer;
    transition: $transition;
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      svg {
        margin-bottom: 6px; } }
    svg {
      width: 14px;
      height: 14px;
      transition: $transition;
      margin-right: 6px;
      fill: currentColor;
      @include media-breakpoint-up(xxl) {
        margin-right: 16px; } }
    &_cart {
      background-color: $body-color;
      color: #fff;
      &:hover {
        background-color: $lightgray;
        color: $body-color; } } } }

@mixin circle {
  @include center;
  border-radius: 50%;
  cursor: pointer;
  transition: $transition;
  flex: none;
  user-select: none;
  svg {
    pointer-events: none;
    fill: $body-color; }
  &:hover {
    svg {
      fill: #fff; } } }
@mixin circle-white {
  --color: #fff;
  --color-hover: #{$red};
  background-color: #fff;
  &:hover {
    background-color: var(--color-hover); } }
@mixin circle-gray {
  background-color: $lightgray;
  &:hover {
    background-color: $body-color; } }
@mixin circle-white-gray-border {
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.1);
  &:hover {
    background-color: $body-color; } }

@mixin swiper-navigation {
  $_: &;
  --swiper-navigation-color: #fff;
  --swiper-navigation-hover-color: #{$body-color};
  display: flex;
  .swiper-button-prev, .swiper-button-next {
    @include circle;
    @include wh(var(--swiper-navigation-size));
    position: relative;
    left: auto;
    right: auto;
    margin: 0;
    background-color: var(--swiper-navigation-color);
    &:hover {
      background-color: var(--swiper-navigation-hover-color); }
    @content;
    &::after {
      content: '';
      width: 6px;
      height: 8px;
      clip-path: polygon(0 0, 0% 100%, 100% 50%);
      background-color: #000;
      transition: $transition; }
    &:hover {
      background-color: var(--swiper-navigation-hover-color);
      &::after {
        background-color: #fff; } }
    &.swiper-button-disabled {} }
  .swiper-button-prev {
    &::after {
      transform: scale(-1); } }
  .swiper-button-next {
    margin-left: 10px; } }

@mixin pagination {
  @include media-breakpoint-up(lg) {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: 74px;
    bottom: auto;
    top: auto;
    left: auto;
    text-align: left;
    height: 1px;
    background-color: var(--swiper-pagination-color);
    .swiper-pagination-bullet {
      width: 60px;
      height: 1px;
      opacity: 1;
      background-color: transparent;
      @include media-breakpoint-up(xxl) {
        width: 100px; }
      &.swiper-pagination-bullet-active {
        background-color: var(--swiper-pagination-active-color); } } } }

@mixin navi {
  .swiper-navigation {
    @include swiper-navigation;
    @include media-breakpoint-down(lg) {
      display: none; } }
  .swiper-pagination {
    @include pagination;
    position: static;
    @include media-breakpoint-up(xxl) {
      max-width: 200px;
      margin-right: 146px; }
    @include media-breakpoint-up(xxl) {
      max-width: 300px; } } }

@mixin slide-img-translate {
  overflow: hidden;
  img {
    transition: 1s ease; }
  &.swiper-slide-prev img {
    transform: translateX(25%); }
  &.swiper-slide-active img {
    transform: translateX(0); }
  &.swiper-slide-next img {
    transform: translateX(-25%); } }

@mixin options($_) {
  &__options {
    z-index: 1;
    #{$_}_single & {
      display: flex; }
    #{$_}_slider & {
      flex-direction: column-reverse;
      position: absolute;
      top: 0;
      right: 0;
      @include media-breakpoint-up(lg) {
        display: none; } }
    #{$_}_slider:hover & {
      display: flex; }
    #{$_}_catalog & {
      position: absolute;
      top: 0;
      right: 0; } }
  &__option {
    @include center;
    margin-bottom: 4px;
    cursor: pointer;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.15);
    background-color: #fff;
    transition: $transition;
    @include wh(38px);
    @include media-breakpoint-up(xl) {
      @include wh(42px); }
    &.is-active {
      background-color: $body-color;
      svg {
        fill: #fff; } }
    svg {
      pointer-events: none; }
    &:hover {
      background-color: #1f1f1f;
      svg {
        fill: #fff; } }
    svg {
      @include wh(10px); }
    #{$_}_single & {
      @include wh(36px);
      margin-bottom: 0;
      margin-right: 4px;
      @include media-breakpoint-up(xl) {
        @include wh(50px); } }
    #{$_}_slider & {
      @include media-breakpoint-up(xl) {
        margin-top: -6px;
        margin-bottom: 0;
        &:last-child {
          margin-top: 0; } } }
    &_favorites {}
    &_compare {} } }


@mixin circle-mini {
  @include wh(26px);
  @include center;
  flex: none;
  border-radius: 50%;
  transition: $transition;
  background-color: currentColor;
  opacity: 1 !important;
  &:hover {
    background-color: $lightgray;
    svg {
      fill: currentColor; } }
  svg {
    fill: #fff; } }


@mixin link-gray {
  color: $gray-dark;
  &:hover {
    color: $body-color; } }

@mixin link-dark {
  color: $body-color;
  &:hover {
    color: $gray-dark; } }

@mixin collapse {
  &[data-hide-title] {
    width: 120px;
    &:before {
      content: attr(data-hide-title);
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
      transform: scale(0);
      width: 0; }
    &:after {
      transform: none;
      transition: 0.3s;
      width: 4px;
      height: 3px; }
    &[aria-expanded="true"] {
      &:before {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
        width: auto; }
      span {
        font-size: 0; }
      &:after {
        transform: rotate(180deg); } } } }
