 :root {
  --header-top-offset: 20px;
  @include media-breakpoint-up(md) {
    --header-top-offset: 40px; } }

.index-top {
  background-color: #fff;
  @include media-breakpoint-up(xl) {
    overflow: hidden;
    min-height: 800px; }
  &__inner {
    height: 100%;
    & > * {
      height: 100%; } }
  &__left {
    position: relative;
    z-index: 3;
    height: 100%;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        margin-left: $container-offset * -1; } }
    @include media-breakpoint-down(xl) {
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          margin-right: $container-offset * -1; } } } }
  &__right {
    padding-top: 132px;
    @include media-breakpoint-down(lg) {
      display: none; } }
  &__rightInner {
    height: 100%;
    width: 100%;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset; } } }
  &__item {
    height: 100%;
    display: flex;
    align-items: center;
    @include media-breakpoint-down(xl) {
      display: none; } } }
.top {
  $_: &;
  position: relative;
  z-index: 9;
  padding-top: var(--header-top-offset);
  &[data-theme-color="dark"] {
    background-color: #fff; }
  body.index & {
    top: 0;
    position: absolute;
    width: 100%; }
  body:not(.index) & {
    margin-bottom: 30px;
    background-color: #fff; }
  &__rightInner {
    width: 100%;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset; } } }
  &__right {
    display: flex; }
  &__header {}

  &__headerInner {
    @include media-breakpoint-up(md) {
      padding-right: 0; } } }

.header {
  $_: &;
  [data-theme-color="white"] & {
    color: #fff; }
  &__logo {
    display: inline-flex;
    align-items: flex-end;
    margin-bottom: 8px;
    font-size: 22px;
    line-height: .7;
    text-decoration: none;
    color: $body-color !important;
    letter-spacing: -.025em;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 16px; }
    &:hover {
      opacity: .65; }
    [data-theme-color="white"] & {
      color: #fff !important; }
    svg {
      flex: none;
      width: 31px;
      height: 26px;
      margin-right: 7px;
      [data-theme-color="white"] & {
        fill: #fff; } }
    span {
      @include media-breakpoint-down(md) {
        display: none; } } }
  &__sity-choose {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 38px;
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__social {}
  &__phone {
    margin-left: 18px;
    color: inherit !important;
    text-decoration: none;
    font-size: 13px;
    &:hover {
      opacity: .65; } }
  &__aboveMenu {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 38px; }
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__video-link {
    margin-left: 50px;
    span:hover {
      opacity: .65; } } }

.sity-choose {
  position: relative;
  &__title {
    font-size: 12px;
    font-weight: 400; }
  &__select {
    @include media-breakpoint-down(lg) {
      position: static; } } }

.video-link {
  display: flex;
  align-items: center;
  font-size: 13px;
  color: inherit !important;
  text-decoration: none;
  &__icon {
    @include circle-mini;
    margin-right: 14px;
    color: #f5263f;
    svg {
      width: 10px;
      height: 6px; } } }

.mainNav-toggle {
  display: none;
  @include media-breakpoint-only(xl) {
    display: block; } }

.header-icons {
  display: flex;
  align-items: center;
  @include media-breakpoint-down(xxl) {
    .top & {
      margin-bottom: 29px; } }
  @include media-breakpoint-down(xl) {
    justify-content: flex-end; }
  & > * {
    cursor: pointer;
    color: #1f1f1f !important;
    margin-left: 27px;
    position: relative;
    [data-theme-color="white"] & {
      @include media-breakpoint-down(xl) {
        color: #fff !important; } }
    .count {
      font-size: 10px;
      display: inline-block;
      vertical-align: super;
      font-weight: 700;
      position: absolute;
      top: 5px;
      right: -3px; }
    @include media-breakpoint-up(sm) {
      margin-left: 40px;
      .count {
        top: -5px;
        right: -10px; } }
    @include media-breakpoint-down(sm) {
      margin-left: 15px;
      padding: 10px 10px 10px 12px;
      margin-right: -10px;
      margin-top: -10px; }
    &:first-child {
      margin-left: 0; }
    &:hover, &.is-active {
      opacity: .65; }
    svg {
      @include wh(14px);
      @include media-breakpoint-up(sm) {
        @include wh(12px); }
      fill: currentColor;
      @include media-breakpoint-up(xxl) {
        @include wh(16px); } } }
  &__user_photo {
    padding: 0;
    min-width: 32px;
    max-width: 32px;
    height: 32px;
    display: flex;
    margin-right: -10px;
    margin-left: 25px;
    &.has-alert {
      &:after {
        content: '';
        width: 8px;
        min-width: 8px;
        max-width: 8px;
        height: 8px;
        background: $red;
        border-radius: 50%;
        position: absolute;
        bottom: 0;
        right: 0; } }
    img {
      @include wh(32px);
      object-fit: contain; } }
  &__search {}
  &__favorites {
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__compare {
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__cart {}
  &__user {
    @include media-breakpoint-up(xl) {
      margin-left: auto !important; } }
  &__toggle {
    @include media-breakpoint-up(xl) {
      display: none; } } }
