.info-card {
  $_: &;
  background-color: $lightgray;
  &__wrapper {
    position: relative; }
  &__slider {}
  &__img {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__content {
    background-color: #fff;
    padding: 20px 0;
    color: #1f1f1f;
    min-height: var(--height);
    @include media-breakpoint-up(xl) {
      line-height: 1.5; }
    @include media-breakpoint-down(lg) {
      flex: 1; }
    @include media-breakpoint-up(lg) {
      display: flex;
      position: absolute;
      bottom: 0;
      width: 90%;
      transition: $transition-elems;
      &:hover {
        bottom: 0; } }
    @include media-breakpoint-up(xxl) {
      padding: 40px 0 40px; } }
  &__contentHide {
    max-height: 0;
    overflow: hidden;
    transition: $transition-elems;
    #{$_}__content:hover & {
      max-height: var(--max-height); }
    #{$_}__content.is-active & {
      @include media-breakpoint-down(xl) {
        max-height: var(--max-height); } } }
  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 370px;
    &::after {
      @include media-breakpoint-up(lg) {
        content: '';
        position: absolute;
        bottom: 4px;
        right: 0;
        width: 8px;
        height: 10px;
        clip-path: polygon(0 0, 0% 100%, 100% 50%);
        background-color: #1f1f1f; } } }
  &__sity {
    margin-bottom: 8px;
    font-size: 12px;
    color: #666;
    @include media-breakpoint-up(md) {
      margin-bottom: auto; }
    svg {
      margin-right: 6px; } }
  &__title {
    margin-top: 10px;
    margin-bottom: 8px;
    font-size: 14px;
    line-height: 18px;
    @include media-breakpoint-up(md) {
      margin-bottom: 13px;
      font-size: 16px;
      line-height: 1.25; } }
  &__metro {
    display: flex;
    margin-bottom: 10px;
    font-size: 12px;
    color: #666;
    @include media-breakpoint-up(md) {
      margin-bottom: 16px; } }
  &__metroItem {
    margin-right: 10px;
    svg {
      margin-right: 6px; } }
  &__address {
    font-weight: 400;
    @include media-breakpoint-down(md) {
      font-size: 12px; } }
  &__contact {
    padding-top: 27px;
    margin-bottom: 17px;
    font-weight: 400; }
  &__days {
    font-size: 12px;
    font-weight: 400;
    line-height: 1.3;
    span {
      margin-bottom: 8px;
      font-size: 14px;
      display: block; } }
  &__partner {
    position: absolute;
    top: 0;
    right: 0;
    width: 167px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background-color: #fa002a;
    text-transform: uppercase;
    font-size: 12px;
    svg {
      margin-right: 8px; } }
  &__footer {}
  &__button {
 }    //margin-top: auto
  .swiper-slide {
    height: auto;
    @include media-breakpoint-down(lg) {
      display: flex;
      flex-direction: column; } } }
