.mainSearch {
  $_: &;
  --btn-size: 70px;
  @include media-breakpoint-up(xl) {
    --btn-size: 160px; }
  z-index: 10;
  width: 100%;
  box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.05);
  background-color: #fff;
  @include media-breakpoint-down(sm) {
    position: absolute;
    left: 0;
    width: 100%;
    top: 100%;
    //padding-left: $container-offsets-mobile
    padding-right: 0;
    background: #fff; }
  &__inner {
    body.index & {
      padding-top: 40px; } }
  &__control {
    position: relative;
    @include media-breakpoint-up(md) {
      background: url("~images/search.svg") 0 center no-repeat; }
    input {
      height: 60px;
      padding: 0;
      border: 0;
      box-shadow: none;
      outline: none !important;
      background: transparent;
      width: 100%;
      @include media-breakpoint-up(md) {
        padding-left: 66px; }
      @include media-breakpoint-up(xxl) {
        font-size: 18px; }
      @include placeholder; } }
  &__buttons {
    display: flex;
    align-items: center;
    position: relative;
    @include media-breakpoint-down(md) {
      width: var(--btn-size); } }
  .btn {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: var(--btn-size);
    @include media-breakpoint-down(xl) {
      background-image: url("~images/search.svg?fill=%23fff");
      background-repeat: no-repeat;
      background-position: center;
      text-indent: -10000px; } }
  &__close {
    display: none;
    @include wh(20px);
    margin-left: 40px;
    cursor: pointer;
    background: url("~images/close.svg") 0 center no-repeat;
    @include media-breakpoint-up(xl) {
      display: block; } }
  &__clear {
    display: none;
    position: absolute;
    right: 30px;
    top: 50%;
    transform: translateY(-50%);
    @include wh(16px);
    background: $body-color url("~images/close_small.svg?fill=%23fff") center no-repeat;
    border-radius: 50%;
    cursor: pointer;
    #{$_}.is-control-filled & {
      display: block; } }
  &__list {
    position: absolute;
    top: 100%;
    left: -30px;
    right: calc(var(--btn-size) * -1); }
  &_static {
    padding-right: 0;
    margin-bottom: 80px;
    .mainSearch__control {
      margin-left: 0;
      flex-grow: 1; }
    .mainSearch__buttons {
      width: auto; }
    @include media-breakpoint-down(md) {
      position: static;
      width: calc(100% + 48px);
      margin: 0 -24px 80px; } } }


.searchList {
  display: none;
  background-color: #fff;
  box-shadow: 0px 10px 30px rgba(26, 12, 0, 0.15);
  &__item {
    position: relative;
    display: flex;
    padding: 6px 76px 6px 30px;
    border-top: 1px solid #E6E6E6;
    transition: $transition;
    &:first-child {
      border-top: 0; }
    &:hover {
      background-color: $lightgray; }
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 20px;
      width: 6px;
      height: 8px;
      transform: translateY(-50%);
      background-color: $body-color;
      clip-path: polygon(0 0, 0% 100%, 100% 50%); } }
  &__img {
    width: 84px;
    margin-right: 10px;
    margin-left: -30px; }
  &__content {
    flex: 1;
    padding: 10px 0; }
  &__title {
    display: block;
    font-size: 16px;
    line-height: 1.15;
    margin-bottom: 4px; }
  &__subtitle {
    color: $gray;
    font-weight: 400; }
  &__price {
    display: flex;
    margin-top: 8px;
    font-size: 14px;
    letter-spacing: -0.025em;
    align-items: flex-end;
    line-height: 1; }
  &__oldprice {
    margin-left: 7px;
    color: $gray;
    font-size: 11px;
    text-decoration: line-through; } }


