.sity-choose {
  .dropdown-menu-block .dropdown-item {
    font-size: 14px; }
  @include media-breakpoint-down(lg) {
    .dropdown-menu-block:not(.ask-city-wrap) {
      width: 100vw;
      transform: translate3d(0px, 38px, 0px) !important;
      height: calc(100vh - 150px);
 } } }      //overflow-y: auto

.ask-city {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  &_mob {
    display: none; }
  @include media-breakpoint-down(xl) {
    max-width: 240px;
    margin: 0 auto;
    display: none;
    &_mob {
      display: block; } }
  &-wrap {
    padding: 20px;
    margin-top: 10px;
    box-shadow: 0px 30px 80px rgba(26, 12, 0, 0.08);
    max-width: 280px;
    transition: 0.5s;
    opacity: 0;
    display: block;
    visibility: hidden;
    top: 200%;
    &.show {
      opacity: 1;
      visibility: visible;
      top: 100%; }
    &_mob {
      display: none !important;
      position: absolute;
      top: 60px !important;
      right: 40px;
      max-width: 350px;
      min-width: 350px;
      left: unset;
      &:before {
        display: none; }
      &.show {
        top: 60px;
        right: 40px;
        left: unset;
        max-width: 350px;
        min-width: 350px; } }
    @include media-breakpoint-down(xl) {
      box-shadow: 0px 30px 80px rgba(26, 12, 0, 0.08) !important;
      left: 24px;
      margin-top: 5px;
      max-width: calc(100vw - 48px);
      min-width: calc(100vw - 48px);
      opacity: 0;
      visibility: hidden;
      &.dropdown-menu:not(.ask-city-wrap_mob) {
        display: none; }
      &_mob {
        display: block !important;
        top: 60px;
        right: 40px;
        max-width: 350px;
        min-width: 350px;
        left: unset; }
      &.show {
        opacity: 1;
        visibility: visible;
        top: 100%; } }
    @include media-breakpoint-down(sm) {
      &_mob {
        top: 80px;
        left: 48px;
        max-width: calc(100vw - 48px);
        min-width: calc(100vw - 48px); }
      &_mob.show {
        top: 60px;
        left: 48px;
        display: block;
        max-width: calc(100vw - 48px);
        min-width: calc(100vw - 48px);
        margin-top: 0; } } }
  &__close {
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
    padding: 10px;
    border: none;
    background: #fff;
    &:before {
      content: '';
      @include wh(16px);
      max-width: 16px;
      padding: 0;
      min-width: 16px;
      cursor: pointer;
      border-radius: 50%;
      background: $body-color url("~images/close_small.svg?fill=%23fff") 50% 50% no-repeat;
      display: flex;
      border: none;
      outline: none;
      transition: 0.3s; }
    &:hover:before, &:focus:before {
      background-color: $gray-800; }
    @include media-breakpoint-down(lg) {
      &:before {
        background-size: 6px 6px; } } }
  &__question {
    text-align: center;
    margin-bottom: 17px; }
  &__geo {
    font-weight: 500; }
  &__buttons {
    display: flex;
    align-items: center;
    .btn {
      display: flex;
      white-space: nowrap;
      flex-grow: 1;
      margin-right: 6px;
      font-weight: 700;
      font-size: 12px;
      height: 26px;
      &:last-child {
        margin-right: 0; } }
    @include media-breakpoint-down(lg) {
      margin-right: 12px; } }
  &__choose-btn {
    background: #F7F7F7;
    &:hover, &:focus {
      background: $body-color;
      color: #fff; } } }
