.b-login {
  &__title {
    margin-bottom: 26px;
    font-size: 16px; }
  &__footer {
    margin-top: 60px;
    @include media-breakpoint-down(md) {
      flex-direction: column-reverse;
      margin-top: 30px;
      .btn {
        width: 100%; } } }
  .form-check label {
    padding-top: 1px;
    padding-left: 5px; }
  .b-social {
    @include media-breakpoint-down(xl) {
      margin-bottom: 50px; } } }
