@mixin link {
  display: flex;
  align-items: center;
  width: 100%;
  height: 57px;
  padding-left: $container-offsets-mobile;
  padding-right: $container-offsets-mobile;
  font-size: 16px;
  border-bottom: 1px solid $lightgray;
  &.dropdown-toggle {
    &::after {
      margin-left: auto; } } }

.mainNav {
  --dropdown-offset-top: 30px;
  $_: &;
  margin-bottom: 24px;
  @include media-breakpoint-up(xxl) {
    margin-bottom: 40px; }
  &__link {
    cursor: pointer;
    transition: 0.3s; }
  &__item_dropdown {
    position: relative; }
  @include media-breakpoint-down(xl) {
    margin-left: 0;
    margin-right: 0;
    &__item {
      .dropdown-menu {
        padding: 0;
        margin: 0;
        .dropdown-toggle {
          padding-left: $container-offsets-mobile + 20px;
          background-color: $lightgray;
          border-bottom-color: #ebebeb; } } }
    &__link {
      @include link;
      &.dropdown-toggle {
        &.show {
          background-color: $body-color;
          color: #fff; } } } }
  @include media-breakpoint-up(xl) {
    //position: relative
    display: flex;
    //flex-wrap: wrap
    &__item {
      margin-left: 20px;
      @include media-breakpoint-up(xxl) {
        margin-left: 30px;
        white-space: nowrap; }
      &:first-child {
        margin-left: 0; }
      &.is-parent {
        //position: relative
        //padding-right: 13px
        //&::before
        //  content: ''
        //  position: absolute
        //  right: 0
        //  top: 50%
        //  width: 7px
        //  height: 4px
        //  clip-path: polygon(50% 100%, 0 0, 100% 0)
 }        //  background-color: currentColor
      &.is-active {} }
    &__link {
      position: relative;
      color: currentColor !important;
      font-size: 13px;
      text-decoration: none;
      @media (min-width: 1700px) {
        font-size: 16px; }
      &:hover {
        text-decoration: underline; }
      &:before {
        transition: 0.3s;
        content: '';
        position: absolute;
        width: 10px;
        height: 5px;
        left: 0;
        background-color: #fff;
        top: calc(100% + var(--dropdown-offset-top) - 5px);
        clip-path: polygon(50% 0%, 0 100%, 100% 100%);
        opacity: 0; }
      #{$_}__item.is-active &::before {
        content: '';
        opacity: 1; } } }
  @include media-breakpoint-down(lg) {
    margin: 0; }
  &__toggle {
    position: relative;
    cursor: pointer;
    rect {
      fill: $body-color;
      transition: 0.2s; }
    @include media-breakpoint-down(xxl) {
      margin-left: 0; }
    [data-theme-color="white"] & {
      rect {
        fill: #fff; } }
    @include media-breakpoint-down(xl) {
      display: none; }
    &.show {
      rect:nth-child(1) {
        transform: rotate(-45deg);
        transform-origin: 110% 310%; }
      rect:nth-child(2) {
        opacity: 0;
        visibility: hidden; }
      rect:nth-child(3) {
        transform: rotate(45deg);
        transform-origin: 130% -190%; } } } }

.modal-social {
  display: none;
  padding: 0 24px;
  margin-top: 60px;
  &__line {
    display: flex;
    align-items: center;
    margin-bottom: 40px; }
  &__phone {
    margin-left: 10px; }
  .social {
    & > a {
      width: 32px;
      height: 32px;
      margin-right: 6px;
      margin-left: 0; } }
  .contact2 {
    margin-bottom: 60px; }


  @include media-breakpoint-down(xl) {
    display: block; } }

.dropdown-main-menu-block {
  color: $body-color;
  display: block !important;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  transform: translateY(-20px);
  @include media-breakpoint-down(lg) {
    opacity: 1;
    visibility: visible;
    transform: none;
    transition: 0.3s; }
  &.show {
    opacity: 1;
    visibility: visible;
    transform: none;
    overflow-y: auto; }
  @include media-breakpoint-up(xl) {
    display: none;
    clip: rect(calc(var(--top) + var(--dropdown-offset-top)), auto, auto, auto); }
  .modal-dialog {
    width: 100vw;
    max-width: none;
    margin: 0; }
  .mainNav-content {
    pointer-events: auto; }
  .mainNav-inner {
    box-shadow: 0px 30px 80px rgba(26, 12, 0, 0.1);
    @include media-breakpoint-down(xl) {
      background-color: $lightgray;
      box-shadow: none; }
    @include media-breakpoint-down(lg);
    @include media-breakpoint-up(xl) {
      --paddingY: 50px;
      --padding-right: 140px;
      --margin-left: -100px;
      background-color: #fff;
      @include media-breakpoint-up(xxl) {
        --paddingY: 85px;
        --padding-right: 250px;
        --margin-left: -149px; }
      margin-left: var(--margin-left);
      padding: calc(var(--top) + var(--paddingY) + var(--dropdown-offset-top)) var(--padding-right) var(--paddingY) 40px;
      &::before {
        content: '';
        display: block;
        height: 40px;
        background-color: #fff;
        position: fixed;
        z-index: 1;
        right: 20px;
        left: calc(var(--left) - (var(--margin-left) * -1));
        margin-top: calc(var(--paddingY) * -1); } } }
  .mainNav-wrap {
    //transform: translateY(85px)
    @include media-breakpoint-up(xl) {
      column-count: 3; } } }

.menu-intro {
  ul {
    list-style: none;
    margin: 0;
    padding: 0; }
  &__title {
    transition: 0.3s;
    span {
      position: relative;
      top: -2px;
      vertical-align: super;
      font-size: 10px;
      display: inline-block;
      margin-left: 4px;
      font-weight: 400;
      @include media-breakpoint-up(xl) {
        font-size: 11px; } }
    @include media-breakpoint-down(xl) {
      &.show {
        background: $body-color;
        color: #fff; } } }
  @include media-breakpoint-down(xl) {
    &__img {
      display: none; }
    &__title {
      @include link;
      padding-left: $container-offsets-mobile + 12px;
      border-bottom-color: rgba(0, 0, 0, 0.05);
      font-size: 14px !important; }
    &__subtitle {}

    &__content {}
    ul {
      background-color: #fff;
      padding-left: $container-offsets-mobile + 12px * 2;
      li {
        font-size: 13px;
        line-height: 1.2;
        padding-bottom: 18px;
        &:first-child {
          padding-top: 20px; } }
      a {
        &:hover {} } }
    ul ul {
      display: none; } }
  @include media-breakpoint-up(xl) {
    display: flex;
    margin-bottom: 50px;
    break-inside: avoid;
    &__img {
      flex: none;
      width: 50px;
      margin-right: 20px;
      @include media-breakpoint-up(xxl) {
        width: 88px; }
      img {} }
    &__content {}
    &__title {
      margin-bottom: 13px;
      line-height: 1.2;
      font-size: 16px;
      @include media-breakpoint-up(xxl) {
        font-size: 18px; } }
    ul {
      padding-top: 6px;
      padding-bottom: 6px;
      font-size: 14px;
      line-height: 1.4;
      @include media-breakpoint-up(xxl) {
        font-size: 16px; }
      li {
        margin-bottom: 5px; }
      a {
        color: $gray-dark;
        display: inline-block;
        &:hover {
          color: $body-color; } } }
    ul ul {
      padding-left: 14px;
      font-size: 12px;
      line-height: 1.2;
      font-weight: 400;
      @include media-breakpoint-up(xxl) {
        font-size: 14px; }
      li {
        margin-bottom: 7px; } } } }

