.b-attach {
  position: relative;
  margin-top: -1px;
  border: 1px solid #d9d9d9;
  border-top: 0;
  background-color: $lightgray;
  @include media-breakpoint-up(xl) {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  &__button {
    @include media-breakpoint-down(xl) {
      text-align: center;
      .btn {
        margin: 18px 0; } } }
  .reviewsItemImgs {
    margin-left: 0;
    @include media-breakpoint-down(xl) {
      margin-bottom: 18px; } } }
