.breadcrumbs {
  display: flex;
  margin-bottom: 50px;
  .page-product & {
    @include media-breakpoint-up(xxl) {
      margin-bottom: 0; } }
  @include media-breakpoint-down(sm) {
    display: none; }
  a, span {
    display: block;
    position: relative;
    margin-left: 8px;
    padding-left: 10px;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 0.04em;
    color: $body-color;
    text-decoration: none;
    &:hover {
      text-decoration: underline; }
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: calc(50% - 2px);
      width: 4px;
      height: 5px;
      border-radius: 0;
      background: url(~images/arrow.svg?fill=%23999999) center / contain no-repeat; }
    &:first-child {
      margin-left: 0;
      padding-left: 0;
      &::before {
        content: none; } }
    &:last-child {
      color: #999; } } }
