.compare-menu {
	margin-bottom: 30px;
	.form-switch {
		margin-top: 16px; }
	.form-check-label {
		color: #999999; }
	&__delete {
		margin-top: 12px;
		svg {
			width: 14px;
			height: 14px;
			margin-right: 6px;
			fill: #fff;
			position: relative;
			top: -1px;
			transition: 0s; }
		&:hover {
			svg {
				fill: $body-color; } } }
	&__count {
		display: inline-block;
		vertical-align: top;
		font-size: 10px;
		margin-left: 4px;
		margin-bottom: 5px;
		font-weight: 500; }
	&__content {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include media-breakpoint-down(lg) {
			display: none; } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		max-width: calc(100% - 150px); }
	&__link {
		padding: 18px 30px;
		border: 1px solid #D9D9D9;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		color: $body-color;
		font-size: 14px;
		font-weight: 500;
		margin-left: -1px;
		margin-bottom: -1px;
		transition: 0.3s;
		&:hover {
			background: $body-color;
			color: #fff; }
		&.active {
			background: $body-color;
			color: #fff; } } }
