.review-write {
  margin-bottom: 60px;
  border-top: 1px solid #d9d9d9;
  padding-top: 18px;
  &__title {
    margin-bottom: 41px;
    font-size: 16px; }
  &__stars {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    @include media-breakpoint-down(lg) {
      justify-content: space-between; }
    &-title {
      margin-right: 20px;
      line-height: .7;
      padding-top: 3px; }

    .svg-stars {
      --width: 18px;
      margin-left: 20px;
      @include media-breakpoint-down(xl) {
        --width: 26px; } } }
  &__list {}
  &__item {
    margin-bottom: 30px;
    .form-control {
      padding: 20px; } } }


