.item-character-wrapper {
  margin-bottom: 40px;
  @include media-breakpoint-up(xl) {
    margin-bottom: 90px; } }

.item-character {
  border-top: 1px solid #d9d9d9;
  padding-top: 16px;
  min-width: 0;
  .swiper-navigation {
    @include swiper-navigation;
    --swiper-navigation-size: 50px;
    @include media-breakpoint-up(xl) {
      --swiper-navigation-size: 50px; }
    @include media-breakpoint-up(xxl) {
      --swiper-navigation-size: 50px; }
    --swiper-navigation-color: #{$lightgray};
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 104px;
    display: flex; } }
