.b-reviews {
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 55px;
    @include media-breakpoint-up(xl) {
      margin-bottom: 90px; } }
  &__title {
    margin-bottom: 0;
    font-size: 24px;
    @include media-breakpoint-up(xl) {
      font-size: 40px; } }
  &__navi {
    display: flex;
    align-items: center;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset; } }
    @include media-breakpoint-down(xl) {
      margin-top: 40px; } }
  .swiper-slide {
    @include media-breakpoint-up(xl) {
      display: flex;
      & > * {
          flex: 1; } } }
  &__slider {
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset; } }
    .item {
      width: 100%;
      background: #fff; } }
  @include navi; }

.itemReview {
  background-color: #fff;
  padding: 20px;
  @include media-breakpoint-up(xl) {
    padding: 40px 30px 40px 0; }
  @include media-breakpoint-up(xxl) {
    padding-right: 40px; }
  &__top {
    display: flex;
    align-items: center; }
  &__title {
    font-size: 16px;
    @include media-breakpoint-up(xl) {
      font-size: 14px; }
    @include media-breakpoint-up(xxl) {
      font-size: 16px; } }
  &__sity {
    font-weight: 400;
    color: #666;
    font-size: 12px;
    @include media-breakpoint-up(xxl) {
      font-size: 14px; } }
  &__img {
    position: relative;
    @include wh(60px);
    flex: none;
    margin-right: 12px;
    @include media-breakpoint-up(xl) {
      @include wh(54px); }
    @include media-breakpoint-up(xxl) {
      @include wh(60px);
      margin-right: 20px; }
    span {
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: $lightgray url("~images/user.svg") center center no-repeat; }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%;
      object-fit: cover; }
    svg {
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      @include wh(26px); } }
  &__date {
    margin-bottom: 10px;
    font-weight: 400;
    text-align: right;
    font-size: 12px;
    span {
      display: inline-block;
      margin-left: 10px; } }
  &__btn {
    font-weight: 700;
    font-size: 12px;
    display: inline-flex;
    align-items: center;
    span {
      width: 5px;
      height: 6px;
      clip-path: polygon(0 0, 0% 100%, 100% 50%);
      background-color: #fff;
      margin-left: 4px; } } }

.itemReviewText {
  position: relative;
  margin-bottom: 20px;
  background-color: $lightgray;
  border-radius: 10px;
  padding: 16px;
  color: #666;
  @include media-breakpoint-up(xl) {
    padding: 17px 20px;
    border-radius: 16px;
    &::before {
      content: '';
      position: absolute;
      left: 25px;
      top: -4px;
      @include wh(7px);
      transform: rotate(45deg);
      background-color: $lightgray; } }
  @include media-breakpoint-up(xxl) {
    padding: 26px 30px;
    border-radius: 20px; }
  &__content {
    margin-bottom: 36px; }
  &__item {}
  &__stars {
    display: flex;
    align-items: center;
    .svg-stars {
      --width: 18px;
      margin-left: 10px; } } }
