.services {
  display: flex;
  align-items: center;
  height: 294px;
  text-align: center;
  line-height: 1.4;
  @include media-breakpoint-down(md) {
    background-color: $lightgray; }
  @include media-breakpoint-up(xxl) {
    height: 395px;
 }    //padding-top: 70px
  @include media-breakpoint-down(xxl) {
    br {
      display: none; } }
  &__slider {
    margin-left: -20px;
    margin-right: -20px; }
  &__item {
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset / 2;
        padding-right: $container-offset / 2; } } }
  &__icon {
    margin-bottom: 24px;
    opacity: 0.3;
    svg {
      @include wh(26px); }
    @include media-breakpoint-up(md) {
      margin-bottom: 40px; } }
  &__title {
    margin-bottom: 10px;
    font-size: 18px;
    letter-spacing: -0.025em;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 17px;
      font-size: 22px;
      letter-spacing: -0.02em; } }
  &__text {
    color: $gray; }
  .swiper-wrapper {
    padding: 40px 0;
    @include media-breakpoint-down(sm) {
      padding-top: 0; } }
  .swiper-pagination {
    bottom: 0; } }

