.b-brands {
  --padding-top: 36px;
  @include media-breakpoint-up(md) {
    --padding-top: 90px; }
  $_: &;
  background-color: $lightgray;
  &__topTitle {
    font-size: 24px;
    margin-bottom: 25px;
    letter-spacing: -1px;
    @include media-breakpoint-up(lg) {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 40px; } }
  &__topText {
    max-width: 400px;
    @include media-breakpoint-up(lg) {
      position: absolute;
      bottom: 100px;
      font-size: 18px;
      line-height: 18px; } }
  &__wrapper {
    position: relative; }
  &__slider {}
  &__img {}
  &__text {
    &::after {
      content: '';
      display: block;
      width: 8px;
      height: 10px;
      margin-top: 40px;
      clip-path: polygon(0 0, 0% 100%, 100% 50%);
      background-color: #fff; } }
  &__button {}
  &__item {
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: var(--padding-top);
    cursor: pointer;
    text-decoration: none;
    background-color: #fff;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.03); } }
  &__title {
    @include panel-title;
    @include media-breakpoint-down(md) {
      font-size: 20px;
      line-height: 16px;
      text-transform: uppercase;
      font-weight: 700; }
    #{$_}__content & {
      color: #fff; } }
  &__img {
    margin-left: auto;
    margin-top: auto;
    transition: $transition-elems;
    #{$_}__item:hover & {
      opacity: 0; } }
  &__content {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    background-color: #fa002a;
    z-index: 1;
    overflow: hidden;
    transform: translateY(-100%);
    transition: $transition-elems;
    #{$_}__item:hover & {
      transform: translateY(0); }
    a {
      text-decoration: none;
      color: inherit !important; } }
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: var(--padding-top);
    transform: translateY(100%);
    transition: $transition-elems;
    padding-bottom: 60px;
    @include media-breakpoint-up(xxl) {
      padding-bottom: 90px; }
    #{$_}__item:hover & {
      transform: translateY(0); } }
  &__footer {}
  .swiper-slide {
    height: auto; } }
