.m-compare-menu {
	display: none;
	margin-bottom: 14px;
	@include media-breakpoint-down(lg) {
		display: flex;
		&.m-cities-menu {
			margin: 0 -25px 14px; } }
	&__dropdown {
		width: 100%; }
	.dropdown-compare .dropdown-toggle {
		display: flex;
		align-items: center;
		width: 100%;
		height: 54px;
		padding-left: 16px;
		border: 1px solid #e6e6e6;
		&:after {
			position: absolute;
			right: 30px; } } }
