.b-items {
  $_: &;
  min-width: 0;
  body.index & {
    background-color: #fff; }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 90px; }
  &__navi {
    display: flex;
    align-items: center;
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        padding-left: $container-offset; } }
    @include media-breakpoint-up(lg) {
      justify-content: flex-end; }
    @include media-breakpoint-down(lg) {
      margin-top: 40px; } }
  &__slider {
    //width: 100%
    &_offsetNRight {
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          margin-right: $container-offset * -1; } }
      &.container-fluid {
        margin-right: 0; } }
    #{$_}_main & {
      padding-left: 0;
      @include media-breakpoint-up(lg) {
        @each $breakpoint, $container-offset in $container-offsets {
          @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
            padding-left: $container-offset;
            padding-right: $container-offset; } } } }
    #{$_}_main & {
      @include media-breakpoint-up(lg) {
        margin-bottom: -200px; } }
    //#{$_}_similar &
    #{$_}_similar & {
      .swiper-slide {
        display: flex; }
      .item {
        background: #fff; } }
    #{$_}_similar:not(.b-items_compare) & {
      .item {
        width: 100%; } } }
  &_compare {
    margin-bottom: 50px;
    .item__title {
      will-change: transform; }
    .item__img {
      transition: 1s;
      will-change: transform; }
    .b-items__slider-wrapper {
      z-index: 11;
      transition: .5s;
      will-change: transform; }
    .item {
      transition: 0.3s;
      padding: 20px 20px 40px;
      will-change: transform;
      &:hover {
        box-shadow: 0 20px 20px rgba(26, 12, 0, 0.05); } }
    .item__content {
      padding: 0; }
    .item__priceOld {
      font-size: 11px;
      margin-right: 5px; }
    .item__priceNew {
      font-size: 14px; }
    .item__footerInner {
      transform: translateY(0); }
    .item__img {
      margin: 0 10px;
      @include media-breakpoint-down(xl) {
        margin: 0 30px 0 0;
        img {
          margin: 0 auto 0 0;
          min-height: 100px;
          object-fit: cover;
          max-width: 100%; } } }
    .item__footer {
      height: auto;
      display: flex;
      margin: 0;
      width: 100%;
      justify-content: flex-end; }
    .item__bottom {
      margin-bottom: 15px; }
    .item__title {
      font-size: 14px;
      font-weight: 500;
      line-height: 1.25;
      min-height: 55px;
      @include media-breakpoint-down(xl) {
        font-size: 12px;
        min-height: 45px; } }
    .item__button_cart {
      padding: 14px;
      width: 100px;
      height: 25px;
      flex-grow: 0;
      border-radius: 110px;
      white-space: nowrap;
      font-size: 12px;
      svg {
        margin-right: 6px;
        width: 11px;
        height: 11px; } }
    .item_catalog {
      margin-bottom: 40px; }
    .compare-nav {
      padding-bottom: 0;
      margin-bottom: 40px; }
    &.minimized {
      .item {
        height: 190px; }
      .item__labels {
        display: none; }
      .item__stars {
        display: none; }
      .item__articul {
        display: none; }
      .item__colors {
        display: none; }
      .item__title {
        font-size: 12px; }
      .item__img {
        width: 70px;
        height: 62px;
        margin: 0; }
      .item__options {
        display: flex; }
      .item__option {
        max-width: 38px;
        max-height: 38px;
        margin-right: 4px; }
      .item__slider {
        margin-bottom: 8px; }
      .item__title {
        min-height: 30px; }
      .item__bottom {
        margin: 8px 0;
        padding: 15px 0; }
      .item__footer {
        position: relative;
        z-index: 2;
        top: -50px; }
      .b-items__slider-wrapper {
        top: 0;
        position: sticky;
        height: 190px;
        overflow: hidden;
        transform: none; } }
    &.minimizing {
      .b-items__slider-wrapper {
        transform: translateY(-100%); } }
    @include media-breakpoint-down(xl) {
      .item__price {
        flex-direction: row-reverse;
        justify-content: flex-end;
        align-items: center;
        height: 31px; }
      .item__priceOld {
        margin-right: 0;
        margin-left: 5px; }
      .item__bottom {
        border-top: none; }
      .item__colors {
        margin-bottom: 6px; }
      .item__footer {
        justify-content: flex-start;
        font-size: 12px; }
      .b-items__slider-wrapper {
        margin: 0 -25px;
        padding: 0 25px;
        background: #fff; }
      .item {
        padding: 10px;
        margin-bottom: 10px;
        &:hover {
          box-shadow: none; } }
      .item__labels {
        display: none; }
      .item__articul {
        font-size: 10px; }
      .compare-nav {
        margin-top: 20px; }
      &.minimized {
        .item {
          padding: 10px; }
        .item__price {
          border: none;
          flex-direction: column-reverse;
          align-items: flex-start; }
        .item__priceOld {
          margin-left: 0; }
        .item__footer {
          justify-content: flex-end;
          top: -33px; }
        .item__footerInner {
          width: 100%;
          justify-content: flex-end; }
        .item__slider {
          margin-bottom: -10px; }
        .item__bottom {
          margin: 8px 0 0;
          padding: 0; }
        .b-items__slider-wrapper {
          height: 145px;
          .item__img img {
            max-width: 85%;
            object-fit: contain;
            margin-left: -12px;
            min-height: unset; } }
        .item__img {
          margin: 0; }
        .item__button_cart {
          font-size: 0;
          width: 38px;
          height: 38px;
          svg {
            margin-right: 0; } }
        .item__option {
          max-width: 34px;
          max-height: 34px;
          margin-right: 2px; } } }
    @include media-breakpoint-down(md) {
      .item {
        padding: 10px 3px; }
      &.minimized {
        .b-items__slider-wrapper {
          height: 145px;
          position: fixed;
          top: 0;
          left: 0;
          padding: 0 24px;
          width: calc(100% + 48px);
          .b-items__slider {
            margin: 0 24px; } }
        @include media-breakpoint-down(md) {
          .item {
            padding: 10px 14px 10px 0px; } } } } }
  &_offsetNDown {
    .section-footer_size-md {
      padding-bottom: 40px; }
    @include media-breakpoint-down(md) {
      padding-bottom: 20px; } }
  .swiper-slide {
 }    //display: flex
  //.section-footer
  //  +media-breakpoint-up(xl)
  //    display: none
  body.index & {
    .swiper-navigation {
      --swiper-navigation-color: #{$lightgray}; } } }
