.btn-decorate {
  overflow: hidden;
  position: relative;
  font-size: 13px;
  padding-right: 20px;
  padding-bottom: 2px;
  line-height: 1.6;
  letter-spacing: -0.025em;
  text-decoration: none;
  color: inherit !important;
  display: inline-flex;
  cursor: pointer;
  @include media-breakpoint-up(sm) {
    font-size: 15px; }
  @include media-breakpoint-up(xl) {
    font-size: 16px; }
  &_lg {
    font-size: 16px;
    @include media-breakpoint-up(sm) {
      padding-right: 36px;
      font-size: 18px; }
    @include media-breakpoint-up(xl) {
      font-size: 18px; } }
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -2px;
    width: 5px;
    height: 7px;
    clip-path: polygon(0 0, 0% 100%, 100% 50%);
    background-color: currentColor;
    @include media-breakpoint-up(sm) {
      width: 6px;
      height: 8px; } }
  &_collapse {
    @include collapse;
    &::before {
      width: 7px;
      height: 5px;
      clip-path: polygon(50% 100%, 0 0, 100% 0); }
    &[aria-expanded="true"]::before {
      clip-path: polygon(50% 0, 0 100%, 100% 100%); } }
  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: currentColor;
    transition: transform cubic-bezier(.47,.09,.06,.93) .5s;
    transform: scaleX(1);
    transform-origin: 0 0; }
  &:hover::after {
    transform: scaleX(0);
    transform-origin: 100% 0; }
  svg {
    @include wh(14px);
    position: absolute;
    right: 0;
    bottom: 8px;
    @include media-breakpoint-down(md) {
      @include wh(12px);
      bottom: 7px; } } }

.btn.has-icon {
  display: inline-flex;
  align-items: center; }


.button-more {
  margin-top: 27px;
  @include collapse; }


.btn {
  svg + span {
    margin-left: 8px; } }

.btn-xl {
  padding-top: 21px;
  padding-bottom: 21px; }

.btn-dark {
  &:hover {
    background-color: $lightgray;
    border-color: $lightgray;
    color: $body-color; } }


[data-type="option"] {
  &.is-active {
    border-color: #333;
    background-color: #333;
    background-image: url("~images/check.svg?fill=%237BC537");
    background-repeat: no-repeat;
    background-position: calc(50% + 10px) calc(50% + 10px);
    svg {
      fill: #fff; } } }



.btn-xl {
  @include button-size($btn-padding-y-xl, $btn-padding-x-xl, $btn-font-size-xl, $btn-border-radius-xl); }


