.filepond {
  &--credits {
    display: none; }
  &--drip {
    background: $lightgray !important;
    opacity: 1 !important;
    border-radius: 0 !important; }
  &--root {
    width: 100%;
    margin-bottom: 0 !important;
    display: flex;
    flex-direction: row-reverse;
    padding-right: 30px;
    height: 60px;
    overflow: hidden;
    font-family: $font-family-base !important;
    .filepond--drop-label {
      position: static;
      min-height: 60px !important;
      label {
        white-space: nowrap;
        line-height: 0.7;
        text-align: center;
        text-decoration: none;
        vertical-align: middle;
        cursor: pointer;
        user-select: none;
        border-radius: 50rem;
        font-size: 12px;
        transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
        height: 25px;
        display: inline-flex !important;
        align-items: center;
        justify-content: center;
        padding: 0 11px !important;
        color: #fff;
        background-color: $dark;
        border-color: $dark;
        &:hover {
          background-color: #fff;
          border-color: #fff;
          color: #1f1f1f; } } }
    .filepond--list-scroller {
      width: calc(100% + 11px);
      position: relative;
      left: -11px;
      margin: 0 !important;
      transform: none !important;
      height: 60px;
      overflow: hidden !important;
      display: flex; }
    .filepond--file-info {
      display: none; }
    .filepond--file {
      max-height: 70px;
      max-width: 80px;
      border-radius: 0;
      background: $lightgray;
      height: 60px; }
    .filepond--image-preview {
      background: $lightgray;
      canvas {
        background: $lightgray;
        border-radius: 0;
        min-height: 247px;
        width: auto;
        object-fit: contain; } }
    .filepond--image-preview-wrapper {
      max-height: 80px;
      height: 80px;
      max-width: 90px;
      border-radius: 0;
      background: $lightgray;
      border-right: 2px solid $lightgray;
      img {
        object-fit: cover; } }
    .filepond--list.filepond--list {
      top: -4px; }
    .filepond--image-clip {
      height: 70px !important; }
    .filepond--image-preview-overlay-idle {
      color: transparent; }
    .filepond--item {
      max-height: 60px;
      max-width: 80px;
      margin: 0;
      &[data-filepond-item-state="load-invalid"] .filepond--file {
        background: #9a5a5a;
        position: relative;
        top: 4px;
        border-right: 2px solid $lightgray; } }
    .filepond--file-status {
      font-size: 0; }
    .filepond--file-action-button.filepond--file-action-button {
      position: absolute;
      width: 16px;
      height: 16px;
      right: 6px;
      top: 7px;
      left: unset;
      background: $dark;
      &:hover {
        background: $gray-800; }
      svg {
        width: 22px;
        height: 22px;
        transform: translate(-3px, -3px); } }
    .filepond--image-bitmap {
      height: 247px; }
    [data-filepond-item-state*=error] .filepond--item-panel, [data-filepond-item-state*=invalid] .filepond--item-panel {
      background: $lightgray; }
    .filepond--file-wrapper.filepond--file-wrapper {
      width: 100%;
      border-radius: 0;
      background: $lightgray; }
    @include media-breakpoint-down(md) {
      flex-direction: column-reverse;
      padding-right: 0;
      .filepond--list-scroller {
        height: 100%;
        width: calc(100% + 20px);
        margin: 0 -10px !important;
        left: 0; }
      .filepond--image-preview-wrapper {
        max-width: unset; }
      .filepond--item {
        max-width: 25%;
        overflow: hidden;
        min-height: 60px; }
      .filepond--image-bitmap {
        min-height: 330px;
        width: 100%;
        border-radius: 0;
        background: $lightgray;
        canvas {
          min-height: unset;
          width: 100%; } } } } }

