.social {
  display: flex;
  a:not(.video-link) {
    @include circle-mini;
    margin-left: 6px;
    text-decoration: none;
    &:first-child {
      margin-left: 0; } }
  svg {
    @include wh(16px); }
  &__telegram {
    color: #38a6db; }
  &__viber {
    color: #7f4da0; }
  &__whatsapp {
    color: #67c15e; } }

.social2 {
  display: flex;
  svg {
    @include wh(16px);
    fill: $body-color; }
  a {
    @include circle;
    @include circle-white-gray-border;
    @include wh(46px);
    flex: none;
    margin-left: 4px;
    margin-bottom: 4px;
    text-decoration: none;
    @include media-breakpoint-up(xxl) {
      @include wh(50px); } } }

.contact2 {
  font-weight: 500;
  &__icon {
    @include wh(50px);
    @include center;
    @include circle;
    @include circle-white-gray-border;
    flex: none;
    margin-right: 15px;
    text-decoration: none;
    @include media-breakpoint-down(sm) {
      @include wh(42px); } }
  svg {
    fill: $body-color; }
  a {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    text-decoration: none;
    color: $body-color !important;
    @include media-breakpoint-down(sm) {
      margin-bottom: 4px; } } }

