.product-top {
  background-color: #fff;
  margin-top: -30px;
  padding-top: 30px; }

.product-media {
  $_: &;
  position: relative;
  .product-top & {
    @include media-breakpoint-down(xl) {
      margin-bottom: 60px; } }
  .modal & {
    height: 100%;
    .swiper-slide img {
      cursor: url("~images/kit/decrease.svg"), auto; } }
  &__labels {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
    @include media-breakpoint-up(xl) {
      left: 100%; }
    .modal & {
      display: none; } }
  &__video {
    position: absolute;
    top: 60px;
    z-index: 2;
    background: #fff;
    @include media-breakpoint-down(xl) {
      display: none; }
    .modal & {
      display: none; } }
  @include options($_: $_);
  &__options {
    position: absolute;
    z-index: 3;
    right: 0;
    @include media-breakpoint-up(xl) {
      display: none; }
    .modal & {
      display: none; } } }

.sliderProduct {
  --padding-bottom: 0;
  @include media-breakpoint-down(xl) {
    --padding-bottom: 40px; }
  $_: &;
  display: flex;
  position: relative;
  padding-bottom: var(--padding-bottom);
  cursor: pointer;
  &__inner {}

  .swiper-slide {
    display: flex;
    align-items: center;
    .product-top & {
      justify-content: flex-end;
      height: auto; }
    .modal & {
      justify-content: center;
      height: calc(100vh - var(--thumbs-height) - var(--padding-bottom));
      @include media-breakpoint-up(xl) {
        height: 100vh; }
      img {
        max-height: 100%;
        margin: auto;
        display: block; } } }
  &__img {
    cursor: url("~images/kit/increase.svg"), auto;
    .modal & {
      display: flex;
      height: 100%; }
    .product-top & {
      position: relative;
      max-width: 800px;
      @include media-breakpoint-up(xl) {
        right: -100px; }
      @include media-breakpoint-up(xxl) {
        max-width: 888px;
        right: 0; } } }
  &__zoom {
    cursor: pointer;
    @include wh(20px);
    background-image: url("~images/zoom_in.svg");
    background-position: center;
    background-repeat: no-repeat;
    .modal & {
      background-image: url("~images/zoom_out.svg"); }
    @include media-breakpoint-up(xl) {
      display: none; } }
  &__navi {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 1;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    @include media-breakpoint-down(xl) {
      top: auto;
      bottom: 0;
      width: 100%;
      justify-content: space-between; }
    .modal & {
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          padding-left: $container-offset;
          padding-right: $container-offset; } } } }
  .swiper-navigation {
    @include swiper-navigation;
    width: 150px;
    display: flex;
    .swiper-button-prev, .swiper-button-next {
      background-color: #f7f7f7; }
    @include media-breakpoint-down(xl) {
      display: none; } }
  .swiper-pagination {
    width: auto;
    position: static;
    @include media-breakpoint-up(xl) {
      margin-left: 120px; } } }


.sliderProductThumbs {
  @include media-breakpoint-up(xl) {
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 340px; }
  @include media-breakpoint-up(xxl) {
    max-width: 516px; }
  .product-top & {
    @each $breakpoint, $container-offset in $container-offsets {
      @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
        margin-left: $container-offset * -1;
        margin-right: $container-offset * -1; } } }
  .swiper-wrapper {
    counter-reset: thumb; }
  .swiper-slide {
    position: relative;
    counter-increment: thumb;
    background-color: #000;
    cursor: pointer;
    opacity: 0;
    img {
      width: 100%;
      opacity: 0.97; }
    &::before {
      @include media-breakpoint-up(xl) {
        content: counter(thumb);
        position: absolute;
        z-index: 1;
        top: 10px;
        right: 10px;
        font-size: 12px; } }
    &:hover, &.swiper-slide-thumb-active {
      img {
        opacity: 1; } } }
  .swiper-container-initialized .swiper-slide {
    opacity: 1; } }

.product-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-bottom: 30px;
  font-size: 13px;
  @include media-breakpoint-up(xxl) {
    font-size: 14px; }
  @include media-breakpoint-up(xl) {
    padding-bottom: 60px; }
  &__button_cart.product-content__button {
    &:hover, &:focus {
      background-color: $dark-hover;
      color: #fff; } }
  &__option {
    transition: 0.3s;
    &:not(.is-active):hover {
      background: $dark-hover;
      border-color: $dark-hover;
      svg {
        fill: #fff; } } }
  &__main-title {
    margin-bottom: 36px;
    //font-size: 24px
    @include media-breakpoint-up(xl) {
 }      //font-size: 26px
    @include media-breakpoint-up(xxl) {
 } }      //font-size: 28px
  &__stars {
    display: flex;
    margin-bottom: 10px;
    font-size: 11px;
    color: #999;
    .svg-stars {
      margin-right: 10px; } }
  &__sbl {
    margin-bottom: 6px;
    color: #999;
    span, a {
      display: inline-block;
      margin-left: 4px;
      color: $body-color;
      .sup {
        font-size: 10px; } }
    &_dost {
      span, a {
        @include media-breakpoint-down(xl) {
          margin-left: 0; } } } }
  &__dropdown {}
  &__top {
    margin-bottom: 10px; }
  @include itemFooter;
  &__targets {
    @include media-breakpoint-down(lg) {
      display: none; } }
  &__price {
    @include media-breakpoint-down(xl) {
      display: none; } }
  &__priceNew {
    font-size: 18px;
    @include media-breakpoint-up(xxl) {
      font-size: 24px; } } }

.dropdown-color {
  .dropdown-toggle {
    display: flex;
    align-items: center;
    width: 100%;
    height: 60px;
    padding-left: 0;
    border: 1px solid #e6e6e6;
    padding: 0;
    &::after {
      position: absolute;
      right: 30px; }
    &:hover {
      border-color: $gray;
      box-shadow: 0 10px 15px rgba(13, 6, 0, 0.05);
      transition: $transition; }
    &:focus {
      box-shadow: none; } }
  &__color {
    width: 24px;
    min-width: 24px;
    height: 24px;
    margin: 0 10px;
    border-radius: 50%;
    &_white {
      border: 2px solid #D9D9D9; } }
  .dropdown-color__title {
    overflow-x: hidden;
    text-overflow: ellipsis;
    height: 16px;
    line-height: 1; }
  .dropdown-menu {
    width: 100%;
    max-height: 310px;
    margin-top: 0;
    border: 0;
    box-shadow: 0 10px 15px rgba(13, 6, 0, 0.05);
    padding-top: 0;
    padding-bottom: 0;
    overflow-y: auto;
    transform: translate3d(0px, 60px, 0px) !important;
    -webkit-overflow-scrolling: touch !important;
    &:not(.show) {
      .simplebar-placeholder {
        height: 0 !important; } }
    &::-webkit-scrollbar {
      display: none;
      height: 0;
      width: 0; } }
  .dropdown-item {
    display: flex;
    align-items: center;
    height: 60px;
    padding: 0 15px 0 0;
    border-bottom: 1px solid #E6E6E6;
    &:hover {
      background-color: $lightgray; }
    &:active {
      background: $lightgray;
      color: $body-color; } } }


.product-wrapper {
  margin-bottom: 90px;
  @include media-breakpoint-up(lg) {
    margin-bottom: 170px; }
  @include media-breakpoint-up(xxl) {
    margin-bottom: 220px; } }

