.block-m-delivery {
  display: flex;
  margin-bottom: 17px;
  &__img {
    width: 68px;
    margin-right: 16px;
    flex: none;
    @include media-breakpoint-up(md) {
      width: 80px; }
    img {
      width: 100%; } }
  &__content {}
  &__title {
    line-height: 1.35;
    font-size: 16px; }
  &__time {
    span {
      color: $gray-dark;
      font-weight: 400; } } }

