.to-top {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  bottom: calc(var(--circle-size) * 2);
  z-index: 99;
  width: 100%;
  height: 0;
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
  @include media-breakpoint-up(xl) {
    position: sticky; }
  @each $breakpoint, $container-offset in $container-offsets {
    @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
      padding-right: $container-offset; } }
  &.active {
    opacity: 1;
    visibility: visible; }
  span {
    @include circle;
    @include circle-gray;
    @include center;
    @include wh(var(--circle-size));
    background: rgba(#000, 0.1);
    &::after {
      content: '';
      width: 8px;
      height: 6px;
      clip-path: polygon(50% 0, 0 100%, 100% 100%);
      background-color: #fff;
      transition: $transition; }
    &:hover {
      &::after {
        background-color: #fff; } } }
  .page-product & {
    display: none; } }
