.b-shop {
  $_: &;
  --img-width: 200px;
  @include media-breakpoint-up(md) {
    --img-width: 160px; }
  @include media-breakpoint-up(xxl) {
    --img-width: 296px; }
  margin-top: 40px;
  margin-bottom: 40px;
  @include media-breakpoint-up(md) {
    display: flex;
    flex-wrap: wrap;
    margin-top: 60px;
    margin-bottom: 60px; }
  &__img {
    width: var(--img-width);
    display: block;
    margin-right: 20px;
    @include media-breakpoint-down(xl) {
      margin-bottom: 24px; }
    //img
    //  width: 100%
    //  height: 100%
 }    //  object-fit: cover
  &__content {
    max-width: 320px;
    color: #1f1f1f;
    @include media-breakpoint-up(xl) {
      @each $breakpoint, $container-offset in $container-offsets {
        @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
          margin-left: $container-offset; } } }
    @include media-breakpoint-up(xxl) {
      max-width: 370px; } }
  &__side {
    font-weight: 500;
    @include media-breakpoint-up(md) {
      margin-left: 160px + 20px; }
    @include media-breakpoint-up(xl) {
      margin-left: auto; }
    @include media-breakpoint-up(xxl) {
      padding-right: 110px; } }
  &__sity {
    margin-bottom: 8px;
    font-size: 12px;
    color: #666;
    @include media-breakpoint-up(md) {
      margin-bottom: auto; }
    svg {
      margin-right: 6px; } }
  &__title {
    margin-top: 10px;
    margin-bottom: 8px;
    font-size: 15px;
    line-height: 18px;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 13px;
      font-size: 16px;
      line-height: normal; } }
  &__metro {
    margin-bottom: 10px; }
  &__address {
    font-weight: 400;
    @include media-breakpoint-down(md) {
      font-size: 12px; } }
  &__contact {
    padding-top: 27px;
    margin-bottom: 17px; }
  &__aboveMenu {
    display: flex;
    align-items: center;
    margin-top: 40px;
    //+media-breakpoint-down(md)
 }    //  display: none
  &__social {}
  &__video-link {
    margin-left: 20px; } }
