@import "../../sass/utilities/variables";
@import "../../sass/utilities/functions";

.item {
  $_: &;
  --padding: 24px;
  --thumbs-size: 60px;
  @include media-breakpoint-up(lg) {
    --padding: 24px;
    --thumbs-size: 60px; }
  @include media-breakpoint-up(xxl) {
    --padding: 40px;
    --thumbs-size: 80px; }
  position: relative;
  padding-top: 40px;
  padding-left: var(--padding);
  padding-right: var(--padding);
  &:hover {
    z-index: 9; }
  &_single {
    position: relative;
    padding: 0 1px 0 0;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      background-color: #fff; }
    &:hover {
 } }      //box-shadow: 0 30px 80px rgba(26, 12, 0, 0.08)
  //&_list
  //  +media-breakpoint-up(lg)
  //    margin-bottom: 80px + 120px
  &_reviews {}
  &_catalog {
    @include media-breakpoint-up(lg) {
      margin-bottom: var(--thumbs-size); } }
  &_slider {
    display: flex;
    width: 100%;
    @include media-breakpoint-up(lg) {
      margin-bottom: calc(var(--thumbs-size) + 80px); } }
  &_popup {
    display: flex;
    width: 100%;
    transition: .3s;
    &.is-hover {
      z-index: 2; }
    &:hover {
      z-index: 2;
      @include media-breakpoint-up(xl) {
        box-shadow: 0 30px 80px rgba(26, 12, 0, 0.08); } }
    &.has-more-images:hover {
      @include media-breakpoint-up(xl) {
        width: calc(100% + var(--thumbs-size));
        margin-left: calc(var(--thumbs-size) * -1);
        margin-bottom: 0;
        padding-left: calc(var(--thumbs-size) + var(--padding)); } } }
  &__inner {
    flex: 1;
    min-width: 0;
    position: relative;
    //#{$_}_slider &
    //  +media-breakpoint-up(lg)
    //    margin-top: 40px
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        display: flex;
        border-bottom: 1px solid #e5e5e5; } } }
  &__img {
    //object-fit: cover
    img {
      display: block;
      margin: 0 auto;
      max-width: 86%;
      @include media-breakpoint-up(xxl) {
        max-width: 100%; } }
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        img {
          max-width: 100%; } } } }
  &__stars {
    display: flex;
    margin-bottom: 10px;
    span {
      margin-left: 6px;
      color: #999;
      font-size: 11px;
      font-weight: 400; }
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        display: none; } } }
  &__catalog {
    margin-bottom: 18px;
    font-weight: 400;
    color: #999;
    @include media-breakpoint-down(lg) {
      margin-bottom: 4px;
      font-size: 12px; } }
  &__labels {
    position: relative;
    z-index: 1;
    height: 0;
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        display: none; } } }
  &__title {
    display: block;
    position: relative;
    min-height: 61px;
    margin-bottom: 14px;
    line-height: 1.2;
    font-size: 14px;
    text-decoration: none;
    color: #1f1f1f;
    @include media-breakpoint-up(xxl) {
      min-height: 58px;
      font-size: 17px; }
    @include media-breakpoint-down(lg) {
      min-height: 45px;
      margin-bottom: 6px;
      font-size: 13px;
 }      //line-height: 15px
    span {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
      @include media-breakpoint-up(xxl) {
        -webkit-line-clamp: 3; } } }
  &__articul {
    display: flex;
    font-size: 12px;
    font-weight: 400;
    color: #999;
    span {
      margin-left: 4px;
      color: #1f1f1f;
      font-weight: 500; }
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        display: none; } } }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    margin-bottom: 16px;
    padding-top: 17px;
    border-top: 1px solid #e5e5e5;
    @include media-breakpoint-up(lg) {
      margin-top: 16px; }
    @include media-breakpoint-up(xxl) {
      margin-bottom: 30px; }
    #{$_}_catalog & {
      @include media-breakpoint-down(lg) {
        display: block;
        margin-bottom: 0;
        padding: 0;
        border-top: 0; } }
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        display: none; } } }
  &__colors {
    #{$_}_catalog & {
      @include media-breakpoint-down(lg) {
        margin-bottom: 20px; } } }
  &__content {
    flex: 1;
    #{$_}_catalog & {
      @include media-breakpoint-up(lg) {
        padding: 0 20px; }
      @include media-breakpoint-up(xxl) {
        padding: 0 32px; } } }
  &__price {
    display: flex;
    align-items: center;
    #{$_}_catalog & {
      @include media-breakpoint-down(lg) {
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        height: 60px;
        border-top: 1px solid #f7f7f7;
        border-bottom: 1px solid #f7f7f7;
        @each $breakpoint, $container-offset in $container-offsets {
          @include media-breakpoint-up($breakpoint, $grid-breakpoints) {
            margin-left: $container-offset * -1;
            padding-left: $container-offset; } } } } }
  &__priceOld {
    margin-right: 16px;
    text-decoration: line-through;
    font-size: 14px;
    color: #999;
    letter-spacing: -0.35px;
    @include media-breakpoint-down(xxl) {
      margin-right: 8px;
      font-size: 11px;
      letter-spacing: -0.3px; } }
  &__priceNew {
    font-size: 17px;
    letter-spacing: -0.05em;
    @include media-breakpoint-down(xxl) {
      font-size: 14px;
      letter-spacing: -0.05em; } }
  @include options($_: $_);
  &__footer {
    height: 0;
    overflow: hidden;
    margin: 0 calc(var(--padding) * -1);
    transition: 0.3s;
    #{$_}_catalog & {
      @include media-breakpoint-down(lg) {
        display: flex;
        justify-content: flex-end;
        height: var(--thumbs-size);
        margin-top: calc(var(--thumbs-size) * -1); } }
    #{$_}_popup:hover & {
      @include media-breakpoint-up(xl) {
        height: var(--thumbs-size); } } }
  &__footerInner {
    display: flex;
    transition: 0.3s;
    @include media-breakpoint-down(lg) {
      width: 50%; }
    @include media-breakpoint-up(lg) {
      transform: translateY(-100%);
      #{$_}_popup:hover & {
        transform: translateY(0); } } }
  &__button {
    flex: 1;
    height: var(--thumbs-size);
    letter-spacing: -0.07px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: $body-color;
    cursor: pointer;
    transition: $transition;
    svg {
      width: 14px;
      height: 14px;
      transition: $transition;
      margin-right: 6px;
      fill: currentColor;
      @include media-breakpoint-up(xxl) {
        margin-right: 16px; } }
    &_fast {
      background-color: $lightgray;
      @include media-breakpoint-down(lg) {
        display: none; }
      &:hover {
        background-color: $body-color;
        color: #fff; } }
    &_cart {
      background-color: $body-color;
      color: #fff;
      @include media-breakpoint-down(xl) {
        z-index: 3; }
      &:hover {
        background-color: $lightgray;
        color: $body-color; } } }
  &__slider {
    z-index: 0;
    margin-bottom: 16px;
    @include media-breakpoint-up(xxl) {
      margin-bottom: 9px; }
    #{$_}_reviews & {
      @include media-breakpoint-down(xl) {
        width: 80px;
        flex: none;
        margin-right: 12px;
        margin-left: 0; } }
    .swiper-button-prev, .swiper-button-next {
      @include wh(50px);
      border-radius: 50%;
      &::after {
        content: '';
        width: 6px;
        height: 8px;
        clip-path: polygon(0 0, 0% 100%, 100% 50%);
        background-color: #000;
        transition: $transition; }
      &:hover {
        border: 1px solid rgba(0, 0, 0, 0.15); } }
    .swiper-button-prev {
      left: 0;
      &::after {
        transform: scale(-1); } }
    .swiper-button-next {
      right: 0; } }
  &__thumbsInner {
    width: var(--thumbs-size);
    height: 100%;
    transition: 0.3s;
    transform: translateX(-20%);
    #{$_}_popup:hover & {
      @include media-breakpoint-up(xl) {
        transform: translateX(0); } } }
  &__thumbs {
    order: -1;
    display: none;
    @include media-breakpoint-up(xl) {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      display: block;
      width: 0;
      background-color: #fff;
      overflow: hidden;
      transition: .3s; }
    #{$_}_popup:hover & {
      width: var(--thumbs-size); }
    .swiper-slide {
      cursor: pointer;
      height: var(--thumbs-size);
      margin-bottom: 10px;
      opacity: 0.2;
      transition: $transition;
      &:hover {
        opacity: .7; }
      &.swiper-slide-thumb-active {
        opacity: 1; } }
    .swiper-navigation {
      width: 0;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0; }
    .slider-button-prev, .slider-button-next {
      @include wh(var(--thumbs-size));
      position: relative;
      border-radius: 0;
      margin: 0;
      background-color: $body-color;
      cursor: pointer;
      transition: $transition;
      &.swiper-button-disabled {
 }        //opacity: 0
      &::after {
        content: '';
        position: absolute;
        width: 8px;
        height: 6px;
        clip-path: polygon(50% 100%, 0 0, 100% 0);
        background-color: #fff;
        transition: $transition;
        left: calc(50% - 4px);
        top: 50%; }
      &:hover {
        background-color: $lightgray;
        &::after {
          background-color: $body-color; } } }
    .slider-button-prev {
      &::after {
        transform: scale(-1); } }
    .slider-button-next {} }
  &__thumbsSlider {
    height: calc(100% - var(--thumbs-size) * 2);
    //.slider-button-prev.swiper-button-disabled + &
 } }    ////padding-top: 20px


.item-options {
  margin-bottom: 15px;
  &__option {
    display: flex;
    align-items: center;
    font-size: 12px;
    @include media-breakpoint-up(xl) {
      font-size: 13px; }
    @include media-breakpoint-up(xxl) {
      font-size: 14px; }
    span {
      margin-right: 6px;
      color: #999; }
    span.color {
      border-radius: 50%;
      @include wh(12px); } } }
